<template>

  <el-container class="main-container">

    <el-header class="top-header" style="flex-grow:0;">

      <div style="min-width: 350px;display: flex;">
        <div @click="toBrief"
          class="hoverCursor">
          <img :src="SysLogo"
            style="height: 40px;margin-top: 5px;" />
          <img :src="SylTitle"
            style="height: 40px;margin-top: 5px;" />
        </div>

      </div>

      <div class="custom-menu">

        <span @click="toPrivate"
          :class="[pathMatch('private')? 'navi-focus' : '']">我的仓库</span>
        <span @click="toPublic"
          :class="[pathMatch('public')? 'navi-focus' : '']">公有仓库</span>
        <span @click="toBrief"
          :class="[pathMatch('brief')? 'navi-focus' : '']">流程</span>
        <span @click="toNews"
          :class="[pathMatch('news')? 'navi-focus' : '']">消息</span>
      </div>

      <div style="width: 150px;display: flex;align-items: center;">
        <span style="margin-right: 22px;font-family: Microsoft Yahei;width: 100px;text-align: right;"
          :title="this.$store.state.userInfo.name"
          class="spanEllipsis">
          {{this.$store.state.userInfo.name}}
        </span>
        <el-dropdown @visible-change="dropDown"
          style="min-width: 50px;height: 50px;"
          trigger="click">
          <div style="display: flex;align-items: center;height: inherit;width: 50px;">
            <div class="icon-focus"
              :class="{'icon-focus-open':dropdown}"
              style="display: flex;align-items: center;">
              <div style="font-size: 32px;color: #66b1ff">
                <i class=" fas fa-user-circle  "></i>
              </div>
              <div style="font-size: 16px;">
                <i class="fas fa-sort-down"></i>
              </div>
            </div>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="personalInfo">

              <i class="fas fa-cog"></i>
              个人设置</el-dropdown-item>
            <el-dropdown-item divided
              @click.native="logout">
              <i class="fas fa-sign-out-alt"></i>
              退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>

    <el-container style="flex-grow:1;">
      <el-aside v-if="this.func.isLogin()&this.isOpened"
        class="left-aside">

        <sidebar></sidebar>

      </el-aside>

      <el-main class="Main">
        <div style="display:flex;flex-direction:column;height:100%;">
           <div style="height: 20px;margin-top: 20px;flex-grow:0;"
          v-if="this.func.isLogin()&this.isOpened">

          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/brief' }">
              <i class="el-icon-location-outline"></i>
            </el-breadcrumb-item>
            <el-breadcrumb-item v-for="item in $route.matched"
              :key="item.path">
              {{ item.name }}
            </el-breadcrumb-item>
          </el-breadcrumb>

        </div>
        <keep-alive>
          <router-view v-if="this.isKeepAlive()"
            :class="[this.$store.getters.isOpened ? 'comp-container-open' : 'comp-container-close']"></router-view>
        </keep-alive>
        <router-view v-if="!this.isKeepAlive()"
          :class="[this.$store.getters.isOpened ? 'comp-container-open' : 'comp-container-close']"></router-view>

        </div>
       
      </el-main>

    </el-container>
  </el-container>

</template>

<script>
import Sidebar from './Sidebar'
import { mapGetters } from 'vuex'
import Cookies from 'js-cookie'
import global_ from './global.vue'
import SysLogo from '@/assets/logo.svg'
import SylTitle from '@/assets/text.svg'
export default {
  components: {
    Sidebar
  },
  data() {
    return {
      SysLogo: SysLogo,
      SylTitle: SylTitle,
      bgColor: '',
      fontColor: '',
      sysName: 'SYSLINK',
      collapsed: false,
      sysUserName: '',
      sysUserAvatar: '',
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      dropdown: false
    }
  },
  computed: {

    ...mapGetters([
      'isOpened',

    ]),
    aaa() {
      return this.isOpened
    },

    bbb() {
      return !this.isOpened
    }
  },
  methods: {
    logout: function () {
      this.$confirm('确认退出吗?', '提示', {
        type: 'warning',
        closeOnClickModal: false
      }).then(() => {
        this.$router.push('/logout');
      }).catch(() => {

      });
    },
    personalInfo() {
      this.$router.push('/personalInfo');
    },

    toBrief() {

      this.$router.push('/brief');
    },
    toPrivate() {

      this.$router.push('/model/MyIndex');
    },
    toPublic() {

      this.$router.push('/model/index');
    },
    toNews() {

      this.$router.push('/myNews');
    },


    collapse: function () {

      this.$store.dispatch('ToggleSideBar');
      console.log(this.$store.state.opened);
    },
    pathMatch(path) {
      if (this.$route.matched.some(route => {
        if (route.meta.path != undefined) {
          if (route.meta.path == path) {
            return true
          }
        }
      })) {
        return true
      }

    },
    dropDown(value) {
      this.dropdown = value;
    },

    isKeepAlive() {
      if (this.$route.meta.keepAlive != undefined) {
        if (this.$route.meta.keepAlive) {
          return true;
        }
      }
    }

  },
  mounted() {
    let user = sessionStorage.getItem('user');
    if (user) {
      user = JSON.parse(user);
      this.sysUserName = user.name || '';
      this.sysUserAvatar = user.avatar || '';
    }


  },

}

</script>

<style scoped >


.top-header {
  display: inline-flex;
  border-bottom: solid 2px #e6e6e6;
  height: 52px !important;
  padding: 0px 10px 0px 10px;
  background-color: #fafafa;
}

.Main {
  height: inherit;
  padding: 0px !important;
  overflow: hidden;
}

.left-aside {
  width: 230px !important;
}

.el-menu--horizontal > .el-menu-item {
  height: 50px !important;
  line-height: 50px !important;
  background-color: #fafafa !important;
  /* border-bottom: none;*/

  font-size: 16px;
}

.el-menu--horizontal > .el-menu-item:hover {
  /*color: #409EFF !important;*/
  background-color: #ebebeb !important;
}

.el-menu--horizontal {
  border-bottom: none;
}

.el-menu--horizontal > .el-menu-item.is-active {
  font-weight: 600;
}

.icon-focus:hover {
  cursor: pointer;
  background: #ebebeb;
}

.icon-focus:focus {
  background: #ebebeb;
}

.icon-focus-open {
  background: #ebebeb;
}

.navi-focus {
  font-weight: 600;
  color: #409eff !important;
  border-bottom: solid 2px #409eff !important;
}

.main-container {
  height: 100%;
  width: 100%;
  min-width: 1000px;
  min-height: 500px; 
}

.comp-container-open {
  /* height: calc(100%-40px) !important;
  height: -moz-calc(100% - 40px) !important;
  height: -webkit-calc(100% - 40px) !important; */
  flex-grow: 1;
  overflow: hidden;
  margin-right: 10px;
}

.comp-container-close {
  height: inherit;
  overflow: hidden;
  margin-right: 10px;

}

.custom-menu {
  height: 50px;
  flex: auto;
  min-width: 400px;
}

.custom-menu > span {
  height: inherit;
  font-size: 16px;
  margin: 0 auto;
  display: inline-block;
  padding: 0px 20px 0px 20px;
  line-height: 50px;
  cursor: pointer;
}
</style>