<template>
  <section class="news-view">
    <div style="height: 50px;margin-top: 10px;min-width: 500px;">
      <el-form :inline="true"
        :model="actionModel"
        ref="searchForm">

        <el-form-item>
          <el-input v-model="actionModel.name"
            @keyup.enter.native="query"
            placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="actionModel.repoName"
            @keyup.enter.native="query"
            placeholder="对象"></el-input>
        </el-form-item>
        <el-form-item>
          <el-date-picker v-model="actionModel.time"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
            size="small"
            @click="query">查询
            <i class="el-icon-search el-icon--right"></i>
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
            size="small"
            @click="reset">重置
            <i class="el-icon-refresh el-icon--right"></i>
          </el-button>
        </el-form-item>

      </el-form>
    </div>

    <el-table :data="actions"
      highlight-current-row
      height="100%"
      stripe
      ref="multipleTable"
      class="actionTable">

      <el-table-column type="index"
        min-width="120">
      </el-table-column>

      <!--   <el-table-column
        label="用户名"
        prop="user.name"
        min-width="180">
      </el-table-column> -->
      <el-table-column label="用户"
        prop="act_user_name"
        min-width="180"
        :show-overflow-tooltip=true>

      </el-table-column>
      <el-table-column label="操作"
        prop="op_type"
        min-width="180"
        :formatter="typeFormatter">

      </el-table-column>

      <el-table-column min-width="180"
        label="对象"
        prop="repo_name">
        <template slot-scope="scope">

          <span style="cursor: pointer;text-decoration: underline;"
            @click="toModel(scope.row)"
            class="tableEllipsis"
            :title="scope.row.repo_name">{{scope.row.repo_name}}</span>
        </template>

      </el-table-column>

      <el-table-column min-width="180"
        label="日期"
        prop="created_unix"
        :formatter="dateFormatter">

      </el-table-column>

    </el-table>

    <el-pagination @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pager.pageIndex"
      :page-sizes="[10, 30, 50, 100]"
      :page-size="pager.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pager.total"
      style="margin-top: 10px;float: right;">
    </el-pagination>

  </section>
</template>

<script>

export default {

  data() {
    return {
      actions: [],
      persistActions: [],
      pager: {
        total: 1,
        pageSize: 10,
        pageIndex: 1,
        currentPage: ''
      },
      actionModel: {
        name: '',
        repoName: '',
        time: ''
      }
    }
  },
  methods: {

    getActions() {
      this.$http.post('api/action/getAssosiation?pageIndex=' + this.pager.pageIndex + '&pageSize=' + this.pager.pageSize).then((res) => {

        this.actions = res.data.actions;
        this.persistActions = res.data.actions;
        this.pager.total = res.data.total;
        

      }).catch(function (error) {
        console.log(error);
      });

    },
    dateFormatter: function (row, column) {
      if (row.created_unix != null) {
        moment.locale('zh-cn');
        return moment(moment.unix(row.created_unix)).format('YYYY-MM-DD HH:mm');
      }

    },
    typeFormatter(row, column) {
      if (row.op_type != null) {
        if (row.op_type == 1) {
          return "创建仓库";
        }
        if (row.op_type == 2) {
          return "重命名仓库";
        }
        // if (row.op_type == 3) {

        // }
        // if (row.op_type == 4) {

        // }
        if (row.op_type == 5) {
          return "提交代码";
        }
        // if (row.op_type == 6) {

        // }
        // if (row.op_type == 7) {

        // }
        if (row.op_type == 8) {
          return "转移仓库";
        }
        // if (row.op_type == 9) {

        // }
        // if (row.op_type == 10) {

        // }
        // if (row.op_type == 11) {

        // }
        // if (row.op_type == 12) {

        // }
        // if (row.op_type == 13) {

        // }
        // if (row.op_type == 14) {

        // }
        // if (row.op_type == 15) {

        // }
        if (row.op_type == 16) {
          return "创建分支:" + row.ref_name;
        }
        if (row.op_type == 17) {
          return "删除分支:" + row.ref_name;
        }
        // if (row.op_type == 18) {

        // }
        if (row.op_type == 19) {
          return "fork仓库"
        }


      }
    },
    toModel(row) {
      if (row.repo_user_name === 'admin') {
        this.$router.push('/model/public/' + row.repo_user_name + '/' + row.repo_name + '/packageDiagram')
      } else {
        this.$router.push('/model/private/' + row.repo_user_name + '/' + row.repo_name + '/myPackageDiagram')
      }

    },
    handleCurrentChange(val) {
      console.log(val);
      this.pager.pageIndex = val;
      this.getActions();
    },
    handleSizeChange(val) {
      console.log(val);
      this.pager.pageSize = val;
      this.getActions();
    },
    query() {
      console.log(this.actionModel)
      console.log(this.actions)
      this.actions = []
      for (let i = 0; i < this.persistActions.length; i++) {
        const act = this.persistActions[i];
        if (this.actionModel.name !== '' && act.repo_user_name !== this.actionModel.name) {
          continue
        }
        if (this.actionModel.repoName !== '' && act.repo_name !== this.actionModel.repoName) {
          continue
        }

        if (this.actionModel.time === '') {
          this.actions.push(act)
        } else {
          const startTime = Math.round(this.actionModel.time[0].getTime() / 1000)
          const endTime = Math.round(this.actionModel.time[1].getTime() / 1000)
          if (startTime <= act.created_unix && endTime >= act.created_unix) {
            this.actions.push(act)
          }
        }

      }
    },
    reset() {
      this.actionModel.name = ''
      this.actionModel.repoName = ''
      this.actionModel.time = ''
    }
  },
  mounted() {
    this.getActions();


  }
}

</script>

<style scoped>
.actionTable {
  width: 100%;
  height: calc(100%-50px) !important;
  height: -moz-calc(100% - 50px) !important;
  height: -webkit-calc(100% - 50px) !important;
}
.news-view {
  display: flex;
  flex-direction: column;
}
</style>