<template>
  <section style="height: inherit;display: flex;align-items: center;">
    <p style="display: none;">{{getTreeItem}}</p>
    <!-- <div >-->
    <!--  <el-col :span="24" class="breadcrumb-container"> -->
    <!--	<span style="font-weight: bold;">当前分类:</span>-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item v-for="item in treeItem"
        :key="item.index"
        :title='item.name'>

        <el-tooltip :content="item.name"
          placement="top">
          <span style="display:inline-block;height:inherit;max-width: 100px;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;height: 16px;line-height: 16px;"> {{ item.name }}</span>
        </el-tooltip>

      </el-breadcrumb-item>
    </el-breadcrumb>
    <!--     </el-col> -->
    <!--  </div>-->
  </section>
</template>
<script>
import { mapState, mapGetters } from 'vuex'
export default {
  data() {
    return {
      treeItem: [],
    };
  },
  computed: {
    ...mapState({
      a: state => state.a
    }),
    ...mapGetters(['amsg']),
    getTreeItem() {
      console.log(this.amsg);
      const _this = this;
      let url;
      if (_this.amsg != null && _this.amsg != "") {
         url = '/api/directory/getTreeItem?parent_id=' + _this.amsg + "&userName=" + _this.$store.state.userInfo.name
      }

      _this.$http.post(url)
        .then(function (response) {
          _this.treeItem = response.data.treeItem;
          _this.$emit("uplaodTitle", _this.treeItem)
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },
  methods: {

  }
}

</script>

<style scoped>
.el-breadcrumb {
  /*	line-height: 20px;*/

  /*display: flex;
	align-items: center;*/
}
</style>

