import babelpolyfill from 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './common/css/custom.css'
//  import './assets/theme/theme-green/index.css'
import VueRouter from 'vue-router'
import router from './routes/asyncRoutes'
import Cookies from 'js-cookie'
import { ElTable, ElTableColumn } from 'c_element_table'
import store from './vuex/store'
import Vuex from 'vuex'
import uploader from 'vue-simple-uploader'
import upload from 'vue-upload-component'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
// import Mock from './mock'
import axios from 'axios'
// import 'font-awesome/css/font-awesome.min.css'
import './assets/iconfont/iconfont.css'
import './assets/iconfont/iconfont.js'
// @import url at.alicdn.com/t/font_472907_116i00seniuanhfr.css
import func from './common/js/func'
import constant from './common/js/constant'
import VueCodemirror from '@/assets/vue-codemirror'
import VueIconfont from 'vue-iconfont'
Vue.prototype.$http = axios;
Vue.prototype.func = func;
Vue.prototype.constant = constant;
Vue.config.productionTip = false;
axios.defaults.withCredentials = true;
//  开启debug模式
Vue.config.debug = true;
Vue.use(ElementUI)
Vue.component('c-el-table', ElTable)
Vue.component('c-el-table-column', ElTableColumn)
Vue.use(upload)
Vue.use(uploader)
Vue.use(Vuex)


Vue.use(VueCodemirror, /* { 
  options: { theme: 'base16-dark', ... },
  events: ['scroll', ...]
} */);
//  NProgress.configure({ showSpinner: false });

router.beforeEach((to, from, next) => {

  NProgress.start();

  console.log(from.path)
  console.log(to.path)

  store.dispatch('GetSession').then(() => {
    if (store.getters.session) {

      if (to.path == '/login' || to.path == '/') {
        console.log(2)
        next('/brief')
      } else {
        //  验证用户信息是否失效
        if (store.getters.userInfo) {
          console.log(1)
          if (store.getters.isLoaded) {
            console.log(5)
            next()
          } else {
            console.log(6)
            const token = { auths: '', roles: '' }
            token.auths = store.getters.userInfo.allAuths
            token.roles = store.getters.userInfo.roles

            store.dispatch('GenerateRoutes', token).then(() => {
              router.addRoutes(store.getters.addRouters)
              next({ ...to })
            })
          }

        } else {

          console.log(4)


          store.dispatch('GetUserInfo').then(res => {
            const token = { auths: '', roles: '' }
            token.auths = res.data.userInfo.allAuths
            token.roles = res.data.userInfo.roles
            store.dispatch('GenerateRoutes', token).then(() => {
              router.addRoutes(store.getters.addRouters)
              next({ ...to })
            })

          }).catch(() => {
            store.dispatch('LogOut').then(() => {
              next({ path: '/login' })
            })


          })
          store.dispatch('GetPublicDirId')
          store.dispatch('GetPrivateDirId')

        }
      }
    } else {
      const rememberMe = Cookies.get('syslink')
      console.log(rememberMe)
      if (rememberMe) {
        const userName = { userName: rememberMe };
        store.dispatch('AutoPass', userName).then(() => {



          if (to.path == '/login' || to.path == '/') {

            next('/brief')
          } else {
            console.log(3);
            //  验证用户信息是否失效
            if (store.getters.userInfo) {
              console.log(11)
              if (store.getters.isLoaded) {

                console.log(9)
                next()
              } else {
                console.log(11)

                const token = { auths: '', roles: '' }
                token.auths = res.data.userInfo.allAuths
                token.roles = res.data.userInfo.roles
                store.dispatch('GenerateRoutes', token).then(() => {
                  router.addRoutes(store.getters.addRouters)
                  next({ ...to })
                })
              }

            } else {
              console.log(10)

              store.dispatch('GetUserInfoFirst', userName).then(res => {
                const token = { auths: '', roles: '' }
                token.auths = res.data.userInfo.allAuths
                token.roles = res.data.userInfo.roles
                store.dispatch('GenerateRoutes', token).then(() => {
                  router.addRoutes(store.getters.addRouters)
                  next({ ...to })
                })
              }).catch(() => {
                store.dispatch('LogOut').then(() => {
                  next({ path: '/login' })
                })

              })
              store.dispatch('GetPublicDirId')
              store.dispatch('GetPrivateDirId')
            }
          }
        })
          .catch(() => {
            store.dispatch('LogOut').then(() => {
              next({ path: '/login' })
            })

          })

      } else {

        if (to.path == '/login') {
          next()
        } else {

          next('/login')

        }
      }
    }


  }).catch(() => {
    store.dispatch('LogOut').then(() => {
      next({ path: '/login' })
    })

  })

})

// router.afterEach(transition => {
// NProgress.done();
// });



router.afterEach((to, from) => {
  console.log(to)

  let a;
  for (const route of to.matched) {
    console.log(route)
    if (route.meta) {
      if (route.meta.isOpened) {
        a = true;
        break;
      } else {
        a = false;
        break;
      }
    }
  }

  store.dispatch('FoldAside', a)
  NProgress.done();
})
new Vue({
  // el: '#app',
  // template: '<App/>',
  router,
  store,
  // components: { App }
  render: h => h(App)
}).$mount('#app')

