<template>
  <div>
    {{getVarTree}} {{treeModelVar}}

    <c-el-table stripe
      border
      tree
      id="v-table"
      :data="data"
      height="100%">
      <c-el-table-column label="名称"
        min-width="100"
        prop="name"
        tree
        show-overflow-tooltip>
      </c-el-table-column>
      <c-el-table-column label="变量类型"
        min-width="100"
        prop="type"
        show-overflow-tooltip>
      </c-el-table-column>
      <c-el-table-column label="重定义"
        min-width="100"
        prop="modification"
        show-overflow-tooltip>
      </c-el-table-column>

    </c-el-table>

  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'modelComponent',
  computed: {
    ...mapState({
      modelId: state => state.modelId,
    }),
    ...mapGetters(['modelId', 'treeModelId']),
    propList() {
      return Object.keys(this.props).map(item => ({
        name: item,
      }));
    },
    getVarTree() {
      const _this = this;
      if (_this.modelId == null || _this.modelId == '') {
        //                    this.$router.push({path: '/model/index'});
      } else {
        const url = '/api/component/componentTree?modelId=' + _this.modelId;
        //                    this.$store.dispatch('sendTreeModelId',_this.modelId);
        _this.$http.post(url)
          .then(function (response) {
            _this.data = response.data.data;
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    },
    getVarTree() {
      const _this = this;
      if (_this.modelId == null || _this.modelId == '') {
        //                    this.$router.push({path: '/model/index'});
      } else {
        const url = '/api/component/componentTree?modelId=' + _this.treeModelId;
        _this.$http.post(url)
          .then(function (response) {
            _this.data = response.data.data;
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }

  },
  data() {
    return {
      props: {
        stripe: true,
        border: false,
        showHeader: true,
        showSummary: false,
        showRowHover: true,
        showIndex: false,
        treeType: true,
        isFold: true,
        expandType: false,
        selectionType: false,
      },
      columns: [{
        minWidth: 100,
        label: '名称',
        prop: 'name'
      }, {
        minWidth: 100,
        label: '变量类型',
        prop: 'type'
      }, {
        minWidth: 100,
        label: '重定义',
        prop: 'modification'
      }
      ],
      data: [
      ],
      //                defaultProps: {
      //                    children: 'children',
      //                    label: 'label'
      //                }
    }
  },
  components: {
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.switch-list {
  margin: 20px 0;
  list-style: none;
  overflow: hidden;
}

.switch-item {
  margin: 20px;
  float: left;
}
</style>