  data() {
    this.__currentNode = null;
    const validateName = (rule, value, callback) => {
      const re = new RegExp("^[ a-zA-Z0-9_\u4e00-\u9fa5]+$");
      console.log(value);

      if (!value) {
        callback(new Error('请输入分类目录名称'));
      } else {
        if (re.test(value)) {
          if (value.length > 32) {
            callback(new Error('分类目录名称不得超过32个字符'));
          } else {
            if (value.trim()) {
              callback();
            } else {
              callback(new Error('不能全为空格'));
            }
          }

        } else {
          callback(new Error('只允许输入中文、字母、数字、下划线及空格'));
        }
      }
    };
    return {
      ztreeDataSource: [],
      dataList: [],
      treeDeepCopy: [],
      parentNodeModel: [], // 当前点击节点父亲对象
      nodeModel: null, // 当前点击节点对象
      toReviewFlag: false,
      breadcrumbArray: [],
      uploadFileTitle1: '创建仓库',
      allowToReviewFlag: false,
      uploadCheckFlag: false,
      url: {
        C: '',
        U: '',
        R: '',
        D: ''
      },
      errGif: errGif + '?' + Number(new Date()),
      props: {
        label: 'name',
        children: 'zones'
      },
      count: 1,
      info: false,
      listStatus: 'true',


      modelTotal: '',
      pager: {
        total: 0,
        pageSize: 10,
        pageIndex: 1,
      },
      variable: [],
      varLength: 0,
      drawer: false,
      sorttitles: [{
        key: 'name',
        name: '名称'
      }, {
        key: 'uploadTime',
        name: '上传时间'
      }, {
        key: 'userName',
        name: '作者'
      }],
      tree: {
        url: {
          C: '/api/directory/add?userName=' + this.$store.state.userInfo.name + "&scope=" + false + "&",
          U: '/api/directory/update?scope=' + false + '&',
          R: 'api/directory/list?scope=' + false + "&userName=" + this.$store.state.userInfo.name + "&pageIndex=0&",
          D: '/api/directory/delete?scope=' + false + '&',
        }
      },
      data: {
        treeItem: "",
      },
      filters: {
        name: "",
        Model: true,
      },
      loading: false,
      isBusy: false,
      align: 'center',
      repositories: [],
      allrepositorie: [],
      dialog: {
        title: '增加分类',
        dialogVisible: false,
        submiting: false,
        form: {
          name: '',
          id: '',
          parent_id: 0
