          name: {
            required: true,
            validator: validateName,
            //                              message: '请输入分类名称',
            trigger: 'blur'
          }
        }
      },
      packageDetailDialog: {
        dialogVisible: false,
        SearchDialog: false,
      },
      move: {
        dialogVisible: false,
        publicdialogVisible: false,
