  computed: {

    ...mapState({
      a: state => state.a
    }),
    ...mapGetters(['amsg']),
    getRepos() {

      const _this = this;
      const para = {
        pageSize: this.pager.pageSize,
        pageIndex: this.pager.pageIndex
      };
      let url;
      if (_this.amsg != null && _this.amsg != "") {
        url = '/api/model/list?parent_id=' + _this.amsg +
          "&scope=" + false + "&userId=" + _this.$store.state.userInfo.id
      } else {
        _this.$store.state.amsg = 0;
        return;
        //                    var url = '/api/model/list?parent_id=' + _this.amsg + "&scope=" + false + "&userId=" + _this.$store.state.userInfo.id
      }
      console.log(url);
      _this.$http.post(url)
        .then(function (response) {
          _this.allrepositorie = response.data.repositories;
          _this.pager.total = response.data.repositories.length;
          _this.modelTotal = response.data.repositories.length;
          // _this.varLength = _this.variable.length;
          const filterModel = response.data.repositories.filter(
            (u, index) => {
              if (index < para.pageIndex * para.pageSize && index >= para.pageSize * (para.pageIndex - 1)) {
                return true
              }
            }
          )
          _this.repositories = filterModel;
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },
  methods: {

    createRespo() {
      if (this.$store.state.amsg == this.privateDirId || this.$store.state.nodeId == "" || this.$store.state.nodeId == this.privateDirId) {
        this.$alert('请先选中所属仓库分类！', '提示')
        return
      }
      if (this.$store.state.nodeId == null || this.$store.state.nodeId == "-1" || this.$store.state.nodeId == "-2" || this.$store.state.nodeId == this.privateDirId) {
        this.$alert('请选择普通仓库分类！', '提示')
        return
      }
      this.file1.dialogVisible = true;
    },
    treeAdd(treeItem, event, node) {
      this.__currentNode = node
      Object.assign(this.dialog, {
        title: '增加分类',
        dialogVisible: true,
        form: {
          name: '',
          id: '',
          parent_id: treeItem.id
        }
      })
