    toDetail(row) {
      this.$store.state.oldModelName = row.name
      this.$store.state.modelName = row.name
      sessionStorage.setItem('modelName', row.name);

      if (row.type == "Modelica") {
        this.$router.push('/model/private/' + row.userName + '/' + row.repositoryName + '/myPackageDiagram')
      } else {
        this.packageDetailDialog.dialogVisible = true;
      }
