    // 获取用户列表
    getModel() {
      const para = {
        name: this.filters.name,
        pageSize: this.pager.pageSize,
        pageIndex: this.pager.pageIndex
      };
      const _this = this;
      let url;
      if (_this.amsg != null && _this.amsg != "") {
        url = '/api/model/list?parent_id=' + _this.amsg + "&scope=" + false + "&userId=" + _this.$store.state.userInfo.id
      } else {
        _this.amsg = 0;
        return;
      }
      _this.$http.post(url)
        .then(function (response) {
          _this.allrepositorie = response.data.repositories;
          const searchModel = response.data.repositories.filter(model => {
            //                        if (para.name && model.name.indexOf(para.name) == -1) {
            //                            return false
            //                        }
            //                        else {
            //                            return true
            //                        }
            const re = new RegExp("^.*" + para.name + ".*$", 'i');   // i表示不区分大小写
            return re.test(model.name);
          })
          const filterModel = searchModel.filter(
            (u, index) => {
              if (index < para.pageIndex * para.pageSize && index >= para.pageSize * (para.pageIndex - 1)) {
                return true
              }
            }
          )
          _this.pager.total = searchModel.length;
          _this.repositories = filterModel;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
