    handleDeleted(index, row) {
      console.log(index, row);
      const _this = this;
      this.$confirm('确认删除该模型吗?', '提示', {
        type: 'warning',
        closeOnClickModal: false,
      }).then(() => {
        const userName = _this.$store.state.userInfo.name;
        const repoName = row.name;
        const formData = new FormData();
        formData.append('_csrf', this.$store.getters.csrf);
        formData.append('action', 'delete');
        formData.append('repo_name', repoName);
        const gogsUrl = 'corporate/syslink/' + userName + "/" + repoName + "/settingsJson"
        _this.$http({
          method: 'post',
          url: gogsUrl,
          headers: { 'Content-Type': 'multipart/form-data' },
          data: formData,
        }).then(res => {
          console.log(res)
          const data = res.data
          if (data.code === 0) {
            const url = '/api/model/deleted?modelId=' + row.index;
            _this.$http.post(url)
              .then(function (response) {
                if (response.data.msg == "ok") {
                  _this.$message({
                    message: '删除成功！',
                    type: 'success',
                    duration: 2000
                  });
                  _this.getModel();
                } else {
                  _this.$message({
                    message: '删除失败！',
                    type: 'error',
                    duration: 2000
                  });
                }
              }).catch(function (error) {
                console.log(error);
              });

          } else {
            _this.$message({
              message: data.message,
              type: 'error',
              duration: 2000
            });
          }
        })


      }).catch(() => {
      });
