      const _this = this;
      const checkAlreadyStar = '/api/star/checkAlreadyStar?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(checkAlreadyStar)
        .then(function (response) {
          if (response.data.alreadyStar == "1") {
            _this.deleteStar(item);
          }
          if (response.data.alreadyStar == "0") {
            _this.addStar(item);
          }
        })
