    addStar(item) {
      const _this = this;
      const url = '/api/star/add?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            item.alreadyStar = true;
            item.numberStar += 1;
            _this.modelVariable(item);
          }
        }).catch(function (error) {
          console.log(error);
        });
