    //              if(o.type == 'Modelica'){
    //                  return true;
    //              }
    //              else{
    //                  return false;
    //              }
    //          },
    validateCAEDownload(o) {
      //              if(o.type == 'Modelica'){
      //                  this.style.watch = 'inline-block';
      //                  return true;
      //              }
      //              else{
      //                  this.style.watch = 'none';
      //                  return false;
      //              }
      return false;
    },
    uplaodTitle(breadcrumbArray) {
      console.log(breadcrumbArray);
      this.uploadFileTitle = "上传到:  "
      for (let i = 0; i < breadcrumbArray.length; i++) {
        this.uploadFileTitle += breadcrumbArray[i].name + "  /  "
      }
      this.uploadFileTitle = this.uploadFileTitle.substring(0, this.uploadFileTitle.length - 1)
    },
    closeDialog() {
      this.file.dialogVisible = false;
      this.getModel();
    },
    modelicaCloseDialog() {
      this.file1.dialogVisible = false;
      this.getModel();
    },
    moveModel(index, row) {
      this.move.dialogVisible = true;
      this.CurrentNode = row;
    },
    pushToPublic(index, row) {
      this.move.publicdialogVisible = true;
      this.CurrentNode = row;
    },
    publicDirectoryNameValiate() {
      const _this = this;

      const checkModelUrl = '/api/directory/checkModel?fileName=' + _this.CurrentNode.name + '&directoryId=' + _this.CurrentNode.directoryId
      _this.$http.post(checkModelUrl)
        .then(function (response) {
          if (response.data.status == 0) {
            _this.pushPublic();
          } else {
            _this.$message({
              message: '该分类下已存在同名仓库，请更换目录！',
              type: 'error',
              duration: 2000
            });
          }

        }).catch(function (error) {
          console.log(error)
          _this.$message({
            message: '推送失败，请重新推送！',
            type: 'error',
            duration: 2000
          });
        })
    },
    pushPublic() {

      const _this = this;
      _this.move.submitLoading = true
      const url = '/api/repository/forkRepository?repositoryName=' + _this.CurrentNode.name + '&userName=' + _this.name + '&directoryId=' + _this.CurrentNode.directoryId +
        '&publicDirectoryId=' + _this.SelectedNode
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            _this.$message({
              message: '提交成功！',
              type: 'success',
              duration: 2000
            });
            _this.move.publicdialogVisible = false;
          } else if (response.data.msg == "exist") {
            _this.$message({
              message: '公有仓库已存在同名仓库，如若确实要公开此仓库，请联系管理员',
              type: 'error',
              duration: 2000
            });
          } else if (response.data.msg == "rexist") {
            _this.$message({
              message: '该仓库已被提交过，请勿重复操作',
              type: 'error',
              duration: 2000
            });
          } else {
            _this.$message({
              message: '提交失败！',
              type: 'error',
              duration: 2000
            });
          }
          _this.move.submitLoading = false
        }).catch(function (error) {
          _this.$message({
            message: '提交失败！',
            type: 'error',
            duration: 2000
          });
          _this.move.publicdialogVisible = false;
          _this.move.submitLoading = false
          console.log(error);
        });
    },
    editSubmit1() {
      const _this = this;
      if (_this.SelectedNode == "-1" || _this.SelectedNode == "-2") {
        _this.$message({
          message: '请选择普通仓库分类！',
          type: 'warning',
          duration: 2000
        });
        return
      }
      const url = '/api/model/moveModel?CurrentNodeId=' + _this.currentRow.index + "&SelectedNodeId=" + _this.SelectedNode
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            _this.$message({
              message: '移动成功！',
              type: 'success',
              duration: 2000
            });
            _this.move.dialogVisible = false;
            _this.getModel();
            _this.currentRow = null;
          }
        }).catch(function (error) {
          _this.$message({
            message: '移动失败！',
            type: 'error',
            duration: 2000
          });
          _this.move.dialogVisible = false;
          console.log(error);
        });
    },
    getSelectedNode(data) {
      this.SelectedNode = data;
    },
    transformFile() {
      this.filters.Model = !this.filters.Model;
    },
    seachFiles() {
      this.packageDetailDialog.SearchDialog = true;
      this.$refs.singleDialog.$nextTick(function () {
        console.log("dom渲染完了");
        this.$emit("DomReady")
      });
    },
    DomReady() {
      this.$refs.getSearchList.getFileList(this.filters.name, false);
    },
    showModel(data) {
      this.packageDetailDialog.SearchDialog = false;
      this.packageDetailDialog.dialogVisible = true;
      this.$refs.DetailDialog.$nextTick(function () {
        console.log("dom渲染完了");
        this.$emit("DetailReady", data)
      });
    },
    DetailReady(data) {
      this.$store.dispatch('sendTreeModelId', data.modelId);
      this.$refs.packageDetail.showFile(data);
    },
    openInnerVisible(data) {
      this.file.innerVisible = true;
      this.file.name = data;
    },
    cover() {
      this.file.innerVisible = false;
      this.$refs.privateUpload.coverModel(this.file.name);
    },
    returnToModel() {
      this.file.dialogVisible = false;
      this.file.modelDialog = true;
    },
    showList() {
      this.listStatus = true;
      this.handleCurrent(this.pager.pageIndex - 1);
      this.handleCurrent(this.pager.pageIndex + 1);
    },
    dateFormatter: function (row, column) {
      if (row.createTime != null) {
        return util.formatDate.format(new Date(row.createTime), 'yyyy-MM-dd');
      }
    },
    dateFormatterupdateTime: function (row, column) {
      if (row.updateTime != null) {
        return util.formatDate.format(new Date(row.updateTime), 'yyyy-MM-dd');
      }
    },

    showInfo(info) {
      const _this = this;

      const promise = new Promise(function (resolve, reject) {
        _this.info = !_this.info;
        resolve();
      });

      promise.then(function () {
        console.log('then', _this.info);
        _this.infoWidth();

      });
    },
    infoWidth() {

      const a = 190 + 24 + 2;
      const leftAside = this.$refs.leftAside.$el;
      const rightAside = this.$refs.leftAside.$el;
      const cardWrapper = this.$refs.cardWrapper;
      if (this.info) {
        const b = window.innerWidth - (leftAside.offsetWidth + rightAside.offsetWidth);
        const c = parseInt(b / a, 10);
        if (c >= 1) {
          cardWrapper.style.width = (a * c) + 'px';
        }
      } else {

        const b = window.innerWidth - leftAside.offsetWidth;
        const c = parseInt(b / a, 10);
        if (c >= 1) {
          cardWrapper.style.width = (a * c) + 'px';
        }
      }
    }

  },


  mounted() {
    this.$store.dispatch('sendA', this.$store.getters.privateDirId.data.id);
    //            this.$store.dispatch('sendB', this.$store.getters.privateDirId.data.id);
    const leftAside = this.$refs.leftAside.$el;
    const a = 190 + 24 + 2;
    const b = window.innerWidth - leftAside.offsetWidth;
    const c = parseInt(b / a, 10);
    const cardWrapper = this.$refs.cardWrapper;
    cardWrapper.style.width = (a * c) + 'px';
    const _this = this;
    window.onresize = function () {
      _this.infoWidth();

    };
  }
};

</script>

