<template>
  <section style="overflow: auto;">

    <div style="padding: 30px 15% 0px 15%;">

      <div style="height: 40px;vertical-align: middle;border-bottom: solid 1px #e6e6e6;">
        <h2 style="display: inline;">版本发布</h2>
        <!--       <span style="font-size: 32px">版本发布</span> -->
        <el-button size="small"
          type="primary"
          style="float: right;"
          @click="showAddDialog">发布新版</el-button>
      </div>

      <ul>
        <li v-for="item in releases" :key="item.id"
          style="display: flex;min-height: 200px;">
          <div style="width: 25%!important;box-sizing: border-box;text-align: right;padding:20px 10px 20px 20px">

            <div>
              <span style="display: block;">
                <i class="iconfont icon-biaoqian1"></i>
                {{item.tagName}}
              </span>
              <span style="display: block;margin-top: 5px;">
                {{dateFormatter(item)}}
              </span>

            </div>
            <!-- <span>{{item.sha1}}</span> -->
          </div>
          <span class="dot"></span>
          <div style="width: 75%!important;box-sizing: border-box;border-left: solid 2px #e6e6e6;padding: 20px 20px 20px 20px;">

            <h3>{{item.title}}
              <small style="color: #409EFF;cursor: pointer;"
                @click="showEditDialog(item)">(编辑)</small>
              <!--     <button @click="showEditDialog">123</button> -->
            </h3>
            <p style="margin: 10px 0px 10px 0px;">

              {{item.publisherName}}发布</p>
            <p> {{item.note}}</p>
            <h2 style="margin-top: 10px;">下载附件</h2>

            <ul class="file_list">
              <li>
                <i class="iconfont icon-filezip"></i>
                <span @click="downloadZIP(item)">源代码（ZIP）</span>

              </li>
              <li>
                <i class="iconfont icon-filezip"></i>

                <span @click="downloadTAG(item)"> 源代码（TAR.GZ）</span>

              </li>
            </ul>

          </div>

        </li>
      </ul>

      <!--   <div v-for="item in releases">
           

           <div style="width: 25%!important;">
             <span>123</span>
           </div>
           <div style="width: 75%!important;">
              <span>456</span>
           </div>
        </div> -->

      <el-pagination @current-change="handleCurrentChange"
        :current-page="pager.pageIndex"
        :page-sizes="[10]"
        :page-size="pager.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pager.total"
        style="text-align: center;"
        v-if="releasesLength()">
      </el-pagination>

    </div>

    <el-dialog title="版本发布"
      :visible.sync="addDialog"
      v-if="addDialog"
      width="50%"
      :close-on-click-modal="false">

      <el-form ref="addForm"
        :rules="addFormRules"
        :model="addForm"
        label-width="0px"
        label-position="top">

        <div style="display: flex;">
          <el-form-item prop="tag_name"
            label="标签"
            label-width="80px">
            <div style="display: flex;">
              <el-input v-model="addForm.tag_name"
                placeholder="标签名称">
              </el-input>
              <span style="width: 40px;text-align: center;">@</span>
            </div>

          </el-form-item>
          <!--   
     <span style="width: 40px;">@</span> -->

          <el-form-item label-width="80px"
            label="分支">
            <el-select v-model="addForm.tag_target"
              placeholder="分支">
              <el-option v-for="item in branchs"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <el-form-item label="标题"
          prop="title"
          label-width="80px">
          <el-input v-model="addForm.title"></el-input>
        </el-form-item>

        <el-form-item label="内容"
          prop="note">
          <el-input type="textarea"
            v-model="addForm.content"
            :autosize="{ minRows: 4, maxRows: 10}"></el-input>
        </el-form-item>

        <!-- <el-form-item>
    <el-button type="primary" @click="addSubmit">发布版本</el-button>
    <el-button @click.native="addDialog=false">取消</el-button>
  </el-form-item> -->

      </el-form>

      <span slot="footer"
        class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary"
          @click="addSubmit">发布版本</el-button>
      </span>
    </el-dialog>

    <el-dialog title="编辑发布信息"
      :visible.sync="editDialog"
      v-if="editDialog"
      width="50%"
      :close-on-click-modal="false">

      <el-form ref="editForm"
        :rules="editFormRules"
        :model="editForm"
        label-width="0px"
        label-position="top">

        <div style="display: flex;border-bottom: solid 1px #e6e6e6;">

          <!--  <span style="font-weight: bold;">{{this.editForm.tagName}}</span> -->
          <h3>{{this.editForm.tag_name}}</h3>
          <span>@</span>
          <h3>{{this.editForm.tag_target}}</h3>
          <!--   <span style="font-weight: bold;">{{this.editForm.target}}</span> -->
          <!--        <el-form-item prop="tagName">
    <el-input v-model="editForm.tagName" placeholder="标签名称" > </el-input>
  </el-form-item label-width="0px">
  
     <span style="display: inline-block;width: 40px;text-align: center;line-height: 40px;">@</span>



   <el-form-item label-width="0px">
       <el-select v-model="editForm.target" placeholder="分支">
      <el-option
      v-for="item in branchs"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
    </el-select>
  </el-form-item> -->
        </div>

        <el-form-item label="标题"
          prop="title"
          label-width="80px">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>

        <el-form-item label="内容"
          prop="content">
          <el-input type="textarea"
            v-model="editForm.content"
            :autosize="{ minRows: 4, maxRows: 10}"></el-input>
        </el-form-item>

        <!-- <el-form-item>
    <el-button type="primary" @click="addSubmit">发布版本</el-button>
    <el-button @click.native="addDialog=false">取消</el-button>
  </el-form-item> -->

      </el-form>

      <span slot="footer"
        class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="danger"
          @click="deleteSubmit">删除版本</el-button>
        <el-button type="primary"
          @click="editSubmit">发布版本</el-button>

      </span>
    </el-dialog>

  </section>
</template>

<script>
import util from '../../common/js/util'


export default {


  data() {

    const checkTag = (rule, value, callback) => {
      const re = new RegExp("^[ a-zA-Z0-9_-]+$");
      if (!value) {
        callback(new Error('请输入标签名称'));
      } else {
        if (value.trim()) {
          this.$http.post('api/release/nameExist?tagName=' + value + '&modelId=' + this.$store.getters.modelId).then(res => {
            if (res.data.flag) {
              callback(new Error('该标签已存在'));
            } else {

              callback();
            }
          }).catch(error => {
            console.log(error);

          })

        } else {
          callback(new Error('不能全为空格'));
        }
      }
    };

    const checkTitle = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入标题'));
      } else {
        if (value.trim()) {
          callback();
        } else {
          callback(new Error('不能全为空格'));
        }

      }
    };
    return {
      owner: '',
      repoName: '',

      pager: {
        total: 1,
        pageSize: 10,
        pageIndex: 1,
        currentPage: ''

      },

      repoLink: '',

      releases: [],
      addDialog: false,
      editDialog: false,
      repoInfo: {},
      // addForm:{
      //  repoId:'',
      //  publisherId:'',
      //  tagName:'',
      //  lowerTagName:'',
      //  target:'',
      //  title:'',
      //  sha1:'',
      //  numCommits:'',
      //  note:'',
      //  isDraft:'',
      //  isPrerelease:'',
      //  createdUnix:''

      // },
      addForm: {

        tag_ame: '',

        tag_target: '',
        title: '',
        content: ''


      },
      // editForm:{
      //  repoId:'',
      //  publisherId:'',
      //  tagName:'',
      //  lowerTagName:'',
      //  target:'',
      //  title:'',
      //  sha1:'',
      //  numCommits:'',
      //  note:'',
      //  isDraft:'',
      //  isPrerelease:'',
      //  createdUnix:''

      // },
      editForm: {
        id: '',
        tag_ame: '',

        tag_target: '',
        title: '',
        content: ''



      },
      branchs: [],
      addFormRules: {

        tag_name: [
          { validator: checkTag, trigger: 'blur', required: true }
        ],
        title: [
          { validator: checkTitle, trigger: 'blur', required: true }
        ],

      },
      editFormRules: {

        title: [
          { validator: checkTitle, trigger: 'blur', required: true }
        ],

      }



    }

  },


  methods: {

    getRelease() {

      this.$http.get('api/release/release/' + this.$store.getters.modelId + '?pageIndex=' + this.pager.pageIndex + '&pageSize=' + this.pager.pageSize).then(res => {

        this.releases = res.data.releases;
        this.pager.total = res.data.total;
      })
    },
    // getRepoInfo(){
    //  var _this = this;
    //  console.log(this.repoName)
    //    this.$http.get('corporate/syslink/'+_this.owner+'/'+_this.repoName).then(res=>{
    //    this.repoInfo = Object.assign({},res.data);
    //    this.branchs = this.repoInfo.Branches;
    //    this.repoLink = this.repoInfo.Repository.RepoLink;


    //  })
    // },
    showAddDialog() {

      if (this.repoInfo.Repository) {
        this.addForm = {};
        this.addForm.tag_target = this.repoInfo.Repository.Repository.DefaultBranch;
        this.addDialog = true;
      }


    },
    showEditDialog(item) {
      this.editDialog = true;
      // this.editForm.id = item.id;
      // this.editForm.tagName = item.tagName;
      this.editForm.id = item.id;
      this.editForm.tag_target = item.target;
      this.editForm.tag_name = item.tagName;
      this.editForm.title = item.title;
      this.editForm.content = item.note;
      // this.editForm.isDraft = 0;
      // this.editForm.isPrerelease = 0;
      // this.editForm.numCommits = this.repoInfo.Repository.CommitsCount;
      // this.editForm.publisherId = this.$store.getters.userInfo.id;



      console.log(item)

    },
    addSubmit() {

      this.$refs.addForm.validate((valid) => {

        if (valid) {
          // this.addForm.isDraft = 0;
          // this.addForm.isPrerelease = 0;
          // this.addForm.repoId = this.repoInfo.Repository.Repository.ID;
          // this.addForm.numCommits = this.repoInfo.Repository.CommitsCount;
          // this.addForm.publisherId = this.$store.getters.userInfo.id;
          // // this.addForm.lowerTagName = this.addForm.tagName.toLowerCase();
          // this.addForm.sha1 = this.repoInfo.LatestCommit.ID;

          // let release = Object.assign({},this.addForm);

          //  this.$http.post('api/release/release/80',release).then(res=>{


          //   if(res.data.flag)
          //   {
          //     this.$message({
          //     message: res.data.msg,
          //     type: 'success'
          //   });
          //   }
          //   else
          //   {
          //     this.$message({
          //     message: res.data.msg,
          //     type: 'error'
          //   });

          //   }
          //   this.getRelease();
          //   this.addDialog=false;


          // })

          const formData = new FormData();
          formData.append('_csrf', this.$store.getters.csrf);
          formData.append('tag_name', this.addForm.tag_name);
          formData.append('tag_target', this.addForm.tag_target);
          formData.append('title', this.addForm.title);
          if (this.addForm.content) {
            formData.append('content', this.addForm.content);
          } else {
            formData.append('content', '');
          }


          this.$http({
            method: 'post',
            url: 'corporate/syslink/' + this.owner + '/' + this.repoName + '/releases/new',
            headers: { 'Content-Type': 'multipart/form-data' },

            data: formData,

          }).then(res => {

            if (res.data.flag) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              });

            }
            this.getRelease();
            this.addDialog = false;

          }).catch(error => {
            console.log(error);
          });

        }
      });

    },

    editSubmit() {

      this.$refs.editForm.validate((valid) => {

        if (valid) {

          const formData = new FormData();
          formData.append('_csrf', this.$store.getters.csrf);
          formData.append('tag_name', this.editForm.tag_name);
          formData.append('tag_target', this.editForm.tag_target);
          formData.append('title', this.editForm.title);
          formData.append('content', this.editForm.content);



          this.$http({
            method: 'post',
            url: 'corporate/syslink/' + this.owner + '/' + this.repoName + '/releases/edit/' + this.editForm.tag_name,
            headers: { 'Content-Type': 'multipart/form-data' },
            data: formData,

          }).then(res => {

            if (res.data.flag) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              });

            }
            this.getRelease();
            this.editDialog = false;

          }).catch(error => {
            console.log(error);
          });


        }
      });

    },


    deleteSubmit() {


      const formData = new FormData();
      formData.append('_csrf', this.$store.getters.csrf);

      this.$http({
        method: 'post',
        url: 'corporate/syslink/' + this.owner + '/' + this.repoName + '/releases/delete?id=' + this.editForm.id,
        headers: { 'Content-Type': 'multipart/form-data' },
        data: formData,

      }).then(res => {

        if (res.data.flag) {
          this.$message({
            message: res.data.msg,
            type: 'success'
          });
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          });

        }
        this.getRelease();
        this.editDialog = false;

      }).catch(error => {
        console.log(error);
      });



    },

    dateFormatter(item) {

      if (item.createdUnix != null) {
        return moment(moment.unix(item.createdUnix)).format('YYYY-MM-DD HH:mm');
      }


    },
    downloadZIP(item) {
      location.href = this.repoInfo.Repository.RepoLink + '/archive/' + item.tagName + '.zip';
    },
    downloadTAG(item) {
      location.href = this.repoInfo.Repository.RepoLink + '/archive/' + item.tagName + '.tar.gz';
    },
    handleCurrentChange(val) {
      this.pager.pageIndex = val;
      this.getRelease();
    },
    releasesLength() {
      console.log(12312312312, this.releases);
      if (this.releases) {
        if (this.releases.length > 0) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    init() {
      console.log(this.$store.getters)
      this.repoInfo = this.$store.getters.repoInfo;
      this.branchs = this.$store.getters.repoInfo.Branches;
      this.repoLink = this.$store.getters.repoInfo.Repository.RepoLink;
      this.owner = this.$store.getters.repoInfo.Repository.Owner.Name;
      this.repoName = this.$store.getters.repoInfo.Repository.Repository.Name;
      this.getRelease();

    }

  },
  mounted() {


    // this.$http.get('api/repository/modelInfo?id='+this.$store.getters.modelId).then(res=>{

    // console.log(res)
    // this.owner = res.data.repository.ownerName;
    // this.repoName = res.data.repository.name;
    // this.getRelease();
    // this.getRepoInfo();

    // }).catch(error=>{

    // })
    this.init();



  }
}

</script>

<style scoped>
.dot {
  width: 9px;
  height: 9px;
  background: #ccc;
  position: relative;
  display: block;
  left: 6px;
  top: 20px;
  border-radius: 6px;
  border: 1px solid #fff;
}

.file_list {
  list-style: none;
  border-top: solid 1px #e6e6e6;
  margin-top: 15px;
}
ul.file_list > li {
  border-bottom: solid 1px #e6e6e6;

  display: block;
  text-align: left;
  padding: 10px 0px 10px 0px;
}

ul.file_list > li > span {
  color: #409eff;
  cursor: pointer;
}
</style>