<template>
  <section>

    <el-table :data="collaborators"
      @current-change="currentChange"
      style="width: 100%"
      :show-header="true">
      <el-table-column label="用户名"
        prop="user.name"
        min-width="180">
        <template slot-scope="scope">
          <span class="tableEllipsis"
            :title="scope.row.user.name">{{scope.row.user.name}}</span>
        </template>
      </el-table-column>

      <el-table-column label="权限等级"
        prop="Collaboration"
        min-width="180">
        <template slot-scope="scope">

          <el-dropdown @command="handleCommand"
            trigger="click"
            style="cursor: pointer;">
            <span class="el-dropdown-link">
              <i class="iconfont icon-mima"></i>
              {{modeTrans(scope.row.mode)}}

              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">可读</el-dropdown-item>
              <el-dropdown-item command="2">可写</el-dropdown-item>
              <el-dropdown-item command="3">管理</el-dropdown-item>

            </el-dropdown-menu>
          </el-dropdown>

        </template>
      </el-table-column>

      <el-table-column min-width="180"
        label="操作">
        <template slot-scope="scope">

          <el-button size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>

    </el-table>

    <div style="margin-top: 10px">
      <el-autocomplete value-key="name"
        class="inline-input"
        v-model="inputName"
        :fetch-suggestions="querySearch"
        placeholder="请输入用户名"
        :trigger-on-focus="false"
        @select="select">

        <template slot-scope="props">
          <div :title=" props.item.name">{{ props.item.name }}</div>

        </template>
      </el-autocomplete>
      <el-button type="primary"
        :loading="loading"
        :disabled="!inputName.length>0"
        @click="addCollaborator">添加协作者</el-button>
    </div>

  </section>
</template>

