    //   }).catch(function(error){
    //     console.log(error);
    //   });



    // },

    addCollaborator() {

      let check = true;
      for (const user of this.collaborators) {

        if (user.user.name == this.inputName) {
          check = false;
          this.$message({
            message: '该用户已是协作者',
            type: 'error'
          });
          break;
        }

      }
      if (check) {
        this.loading = true;

        this.$http.post('api/collaboration/addCollaborator?modelId=' + this.modelId + '&userName=' + this.inputName).then((res) => {
          this.loading = false;
          this.refresh();
          // NProgress.done();
          if (res.data.flag) {
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            });
          }

          this.getCollaborators();
        }).catch(error => {
          this.loading = false;
          console.log(error);
        });
      }


    },
    select(item) {
      this.selectItem = item;
      this.selected = true;
      console.log(this.selectItem);
    },

    querySearch(queryString, cb) {

      const _this = this;


      this.$http.post('api/user/queryWithOutPage?name=' + queryString).then((res) => {

        this.userList = res.data.users;


        const users = this.userList;
        // var users = Object.assign({},res.data.users);
        console.log(users);
        const results = queryString ? users.filter(this.createFilter(queryString)) : users;
        // 调用 callback 返回建议列表的数据
        cb(users);
        console.log(users)



      }).catch(function (error) {
        console.log(error);
      });



    },
    createFilter(queryString) {
      return (user) => {
        return (user.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    refresh() {
      this.$emit("dataRefresh");
    },

