    getCollaborators() {
      this.$http.post('api/collaboration/getCollaboratorsByModelId?modelId=' + this.modelId)
        .then(res => {
          this.collaborators = res.data.collaborators;

        }).catch(error => {
          console.log(error);
        });
    },
    currentChange(currentRow, oldCurrentRow) {
      this.currentRow = currentRow;
    },
    handleCommand(command) {
      this.$http.post('api/collaboration/changeMode?id=' + this.currentRow.id + '&mode=' + command)
        .then(res => {
          this.getCollaborators();
          this.refresh();
        }).catch(error => {
          console.log(error);
        });

      //  var _this=this;
      //  let data = {uid:'',mode:''};
      //  data.uid = this.currentRow.ID;
      //  data.mode = command;
      //  this.$http({method:'post',url:'corporate/repo/admin/ccc/settings/collaboration/access_mode'+'?uid='+data.uid+'&mode='+data.mode,
      // headers:{"X-Csrf-Token":this.$store.state.csrf}}).then(res=>{
      //    _this.getCollaboration();
      // }).catch(error=>{
