    handleDelete(index, row) {

      this.$confirm('确认取消该用户的权限吗?', '提示', {
        type: 'warning',
        closeOnClickModal: false
      }).then(() => {

        // NProgress.start();
        const para = { id: row.id };
        this.$http.post('api/collaboration/deleteCollaborator?id=' + row.id).then((res) => {
          this.refresh();
          // NProgress.done();
          if (res.data.flag) {
            this.$message({
              message: '删除成功',
              type: 'success'
            });
          } else {
            this.$message({
              message: '删除失败',
              type: 'error'
            });
          }

          this.getCollaborators();
        }).catch(error => {
          console.log(error);
        });
      }).catch(() => {

      });



      //      this.$http.post('api/collaboration/deleteCollaborator?id='+row.id).
      // then(res=>{


      // }).catch(error=>{
      //   console.log(error);
      // });

      // let data = {ID:''};
      // data.ID = row.ID;
      //  this.$http({method:'post',url:'corporate/repo/admin/ccc/settings/collaboration/delete',data:data,
      // headers:{"X-Csrf-Token":this.$store.state.csrf}})


      const data = { ID: '' };
      data.ID = row.ID;



      // axios({method:'get',url:'http://localhost/corporate/api/v1/repos/admin/ccc/collaborators/admin/',
      //       data:data,
      //      headers:{"X-Csrf-Token":this.$store.state.csrf,'Content-Type':'json'}


      //    });



