      // headers:{"X-Csrf-Token":this.$store.state.csrf}});


    },
    modeTrans(value) {
      if (value == "1") {
        return "可读"
      }
      if (value == "2") {
        return "可写"
      }
      if (value == "3") {
        return "管理"
      }
    }

  },
  mounted() {

    this.getCollaborators();

    // this.getCollaboration();
    // console.log(this.$route);
    // console.log(this.$router);
    // this.getRepoInfo();



    //        this.$http({url:'corporate/repo/admin/ccc/settings/collaboration/access_mode?uid=4&mode=3',
    //   method:'post',
    //   headers:{"X-Csrf-Token":"rxw6kZ-fy0CogfTjnIwUK3aTHM86MTUyMjIyNzAxMjA1NjM0OTEwMA=="}
    // }).then((res)=>{

    //     console.log(res.data);
    //     this.fileList = res.data;


    //   }).catch(function(error){
