<template>
  <section>
    <div style="">

      <el-form ref="form"
        :model="form"
        :rules="rules"
        label-width="80px"
        style="">

        <div style="width: 100%">
          <el-form-item label="仓库名称"
            prop="name">

            <el-input :maxlength=128
              v-model="form.name"></el-input>

          </el-form-item>

          <el-form-item label="仓库描述"
            prop="desc">

            <el-input :maxlength=200
              type="textarea"
              :autosize="{ minRows: 5, maxRows: 10}"
              placeholder="请添加相关描述"
              v-model="form.desc"></el-input>

          </el-form-item>
          <el-form-item>
            <div style="height: auto;float: right;">
              <el-button type="primary"
                @click.native="createRepo"
                :loading="submitLoading"
                :disabled="allowUpload">
                提交
              </el-button>
              <el-button @click="closeDia">取消</el-button>
            </div>
          </el-form-item>

        </div>
      </el-form>

    </div>

  </section>
</template>
<script>
import upload_breadcrumb from '../nav3/Uploadbreadcrumb.vue'
import uploadPicture from '../nav3/UploadPicuter.vue'
import uploadFile from '../nav3/UploadFile.vue'
import global_ from '../global.vue'
import { mapActions, mapState, mapGetters } from 'vuex'
import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item";

export default {
  components: {
    ElFormItem,
    upload_breadcrumb,
    uploadPicture,
    uploadFile,
    global_
  },
  data() {
    const validateName = (rule, value, callback) => {
      const re = new RegExp("^[A-Za-z_][a-zA-Z0-9_]{0,}$");
      console.log(value);

      if (!value) {
        callback(new Error('请输入仓库名称'));
      } else {
        if (re.test(value)) {
          if (value.length > 128) {
            callback(new Error('仓库名称字符不得超过128个'));
          } else {
            callback();
          }

        } else {
          callback(new Error('只允许输入字母、数字及下划线'));
        }
      }
    };
    const validateDesc = (rule, value, callback) => {

      if (value.length > 128) {
        callback(new Error('仓库描述字符不得超过128个'));
      } else {
        callback();
      }

    };
    return {
      form: {
        name: '',
        desc: '',
      },
      rules: {
        name: [{
          required: true,
          validator: validateName,
          trigger: 'blur'
        }],
        desc: [{
          validator: validateDesc,
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '请输入模型分类',
          trigger: 'blur'
        }],
      },
      submitLoading: false,
      photoUrl: '',
      modelType: [],
      imageUrl: '',
      allowUpload: false,
      name: this.$store.state.userInfo.name,

    }
  },
  computed: {
    ...mapState({
      modelId: state => state.modelId,
      a: state => state.a
    }),
    ...mapGetters(['modelId', 'treeModelId']),
  },
  methods: {
    createRepo() {
      const _this = this;
      this.$refs.form.validate((valid) => {
        if (valid) { // 验证通过
          // 先验证是否出现重名
          const checkModelName = '/api/repository/checkRepoName?repoName=' + this.form.name + '&userName=' + this.name
          _this.$http.post(checkModelName)
            .then(function (response) {
              if (response.data.msg == "success") {
                _this.createRepository(_this);
              } else {
                _this.$message({
                  message: '仓库名称重复！',
                  type: 'error',
                  duration: 2000
                });
              }
            }).catch(function (error) {
              _this.$message({
                message: '请重新输入仓库名称！',
                type: 'error',
                duration: 2000
              });
            })
        }
      })

    },
    closeDia() {
      this.$emit("closeDialog")
    },
    createRepository(_this) {
      _this.submitLoading = true;
      console.log(_this.$store.state.userInfo)
      const userName = _this.$store.state.userInfo.name
      const formData = new FormData();
      //                formData.append('_csrf', "Kd-vG7AmIu_LbHuF67uEP_83NQ06MTUyMzE0Njg4ODI3NDc5OTMwMA==");
      formData.append('_csrf', this.$store.getters.csrf);
      formData.append('user_id', _this.$store.state.userInfo.id);
      formData.append('repo_name', _this.form.name);
      formData.append('description', _this.form.desc);
      formData.append('auto_init', true);
      formData.append('readme', "package.mo");
      _this.$http({
        method: 'post',
        url: 'corporate/repo/create',
        headers: { 'Content-Type': 'multipart/form-data' },
        data: formData,
      }).then(res => {
        if (res.status == 200) {
          const gogsAdd = '/api/repository/gogsAdd?name=' + userName + "&fileName=" + this.form.name + "&desc=" + this.form.desc + "&classId=" + this.$store.state.nodeId
          _this.$http.post(gogsAdd)
            .then(function (response) {
              if (response.data.msg == "success") {
                _this.submitLoading = false;
                _this.$message({
                  message: '创建成功！',
                  type: 'success',
                  duration: 2000
                });
                _this.closeDia();

                // _this.$router.push({path: '/model/myPackageDiagram'});

                _this.$router.push('/model/private/' + userName + '/' + _this.form.name + '/myPackageDiagram')
                // _this.$store.dispatch('sendModelId', response.data.modelId);
                // _this.$store.dispatch('sendTreeModelId', response.data.modelId);
              } else {
                _this.$message({
                  message: '创建失败！',
                  type: 'error',
                  duration: 2000
                });
                _this.submitLoading = false;
              }
            }).catch(function (error) {
              console.log(error)
              _this.submitLoading = false;
              _this.$message({
                message: '发生异常！',
                type: 'error',
                duration: 2000
              });
            })
        } else {
          _this.submitLoading = false;
          _this.$message({
            message: '发生异常！',
            type: 'error',
            duration: 2000
          });
        }
      }).catch(error => {
        console.log(error);
      });
    }
  },
}

</script>
<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>