import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)





function getCsrf(to, from, next) {

    store.dispatch('GetCsrf').then(next())
}

function getRepoInfo(to, from, next) {
    store.dispatch('GetRepoInfo', {})
    axios.get('corporate/syslink/' + to.params.userName + '/' + to.params.repoName).then(res => {
        if (res.status == 200) {
            store.dispatch('GetRepoInfo', res.data)
            next()
            // getModelInfo(to, from, next)
        }
        else {
            next('/404')
        }
    }).catch(error => {

        next('/404')
    })
}

function goRelease(to, from, next) {
    store.dispatch('GetRepoInfo', {})
    let bool = false;

    for (let auth of store.getters.userInfo.directoryAuths) {
        if (auth.directory_id == store.getters.modelInfo.directoryId) {
            if (auth.mode >= 2) {
                bool = true
            }
        }
    }

    if (store.getters.userInfo.roles.some(role => role == 1)) {
        bool = true;
    }

    if (bool) {
        axios.get('corporate/syslink/' + to.params.userName + '/' + to.params.repoName).then(res => {
            if (res.status == 200) {
                store.dispatch('GetRepoInfo', res.data)
                next()
                // getModelInfo(to, from, next)
            }
            else {
                next('/404')
            }
        }).catch(error => {

            next('/404')
        })
    }
    else {
        next('/404')
    }
}

//获取模型信息的同时进行权限判断
function getModelInfo(to, from, next) {
    store.dispatch('GetModelInfo', {})
    store.dispatch('sendModelId', '');
    store.dispatch('sendTreeModelId', '');
    let scope = false;
    if (to.path.includes("public")) {
        scope = true;
    }
    axios.get('api/repository/modelInfo?userName=' + to.params.userName + '&repoName=' + to.params.repoName + '&scope=' + scope).then(res => {
        if (res.status == 200) {
            if (scope) {
                if (res.data.model.userId == store.getters.userInfo.id || store.getters.userInfo.roles.includes(1) || res.data.model.reviewers.some(user => user.user_id == store.getters.userInfo.id)) {
                    store.dispatch('GetModelInfo', res.data.model)
                    store.dispatch('sendModelId', res.data.model.id);
                    store.dispatch('sendTreeModelId', res.data.model.id);
                    next()
                }
                else {


                    if (store.getters.userInfo.directoryAuths.some(auth => auth.directory_id == res.data.model.directoryId)) {
                        store.dispatch('GetModelInfo', res.data.model)
                        store.dispatch('sendModelId', res.data.model.id);
                        store.dispatch('sendTreeModelId', res.data.model.id);
                        next()
                    }
                    else {
                        next('404')
                    }

                }


            }
            else {


                if (res.data.model.userId == store.getters.userInfo.id) {
                    store.dispatch('GetModelInfo', res.data.model)
                    store.dispatch('sendModelId', res.data.model.id);
                    store.dispatch('sendTreeModelId', res.data.model.id);
                    next()
                }

                else {

                    if (store.getters.userInfo.collaboration.some(colla => colla.model_id == res.data.model.id)) {
                        store.dispatch('GetModelInfo', res.data.model)
                        store.dispatch('sendModelId', res.data.model.id);
                        store.dispatch('sendTreeModelId', res.data.model.id);
                        next()
                    }
                    else {
                        next('/404')
                    }

                }


            }


        }
        else {
            next('/404')
        }
    }).catch(error => {

        next('/404')
    })
}





