<template>

  <el-container style="height: 100%;">
    <el-header class="top-header">
      <el-row>
        <el-col :span="4">
          <span style="font-size: 20px;position: relative;top: 10px;">模型：{{form.name}}</span>
        </el-col>
        <el-col :span="4">
          <div style="display: inline-block;position: relative;top: 7px;">
            <a class="ui basic button"
              @click="isAddWatch(form)">
              <span>关注</span>
              <i v-if="form.alreadyWatch == true"
                class="iconfont icon-guanzhu"></i>
              <i v-else
                class="iconfont icon-quxiaoguanzhu01"></i>
              {{form.numberWatch}}
            </a>
          </div>
          <div style="display:inline-block;margin-top: 10px;position: relative;left: 20px;top: 7px;">
            <a class="ui basic button"
              @click="isAddStar(form)">
              <span>点赞</span>
              <i v-if="form.alreadyStar == true"
                class="iconfont icon-guanzhu3"></i>
              <i v-else
                class="iconfont icon-guanzhu4"></i>
              {{form.numberStar}}
            </a>
          </div>
        </el-col>
        <el-col :span="16">
          <div style="display:inline-block;margin-top: 8px;position: absolute;right: 60px;">
            <!--   <el-button type="warning" plain size="small" @click="goRepoSetting">仓库设置</el-button>-->
            <el-button type="warning"
              plain
              size="small"
              @click="showRepoHistory">仓库历史</el-button>
            <el-button type="warning"
              plain
              size="small"
              @click="goPublish">版本发布</el-button>
            <el-button type="warning"
              plain
              size="small"
              @click="download">下载</el-button>
            <el-button type="warning"
              plain
              size="small"
              @click="goFile">文件</el-button>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container class="main-container">

      <el-aside class="left-aside"
        style="overflow: hidden">
        <!--<el-tree-->
        <!--:props="props"-->
        <!--:load="loadNode"-->
        <!--lazy-->
        <!--show-checkbox-->
        <!--@check-change="handleCheckChange"  style="margin-top: 20px;overflow: inherit;">-->
        <!--</el-tree>-->
        <div style="height: 50%">
          <span>模型浏览器</span>
          <modeltree style="height: 100%;overflow-y: auto"></modeltree>
        </div>
        <div style="margin-top: 30px;height: 50%;overflow: auto">
          <span>组件浏览器</span>
          <modeltreeContAndVar style="overflow-y: auto"></modeltreeContAndVar>
        </div>
      </el-aside>
      <el-main style="height: inherit;overflow-y: hidden;">
        <el-tabs value="diagramSVG"
          tab-position="bottom"
          style="height: 100%"
          @tab-click="tabClick">
          {{treeModelVar}}
          <el-tab-pane class="el-tabs--bottom"
            style="height: 100%"
            name="cm">
            <span slot="label">
              <i class="el-icon-document"></i> 文本</span>
            <codemirror v-model="textInfo"
              v-if="cmActive"
              :options="cmOptions"
              style="height: 100%;"></codemirror>
          </el-tab-pane>
          <el-tab-pane style="height: 100%">
            <span slot="label">
              <i class="el-icon-picture"></i> 图标</span>
            <div style="height: 100%;text-align: center;">
              <img v-show="data.iconUrl !== 'default'"
                :src=data.iconUrl
                class="images"
                style="height: 70%;width: 100%;" />
              <img v-show="data.iconUrl === 'default'"
                :src=constant.SVG.DEFAULT
                class="images"
                style="height: 70%;width: 100%;" />
            </div>
          </el-tab-pane>
          <el-tab-pane name="diagramSVG"
            style="height: 100%">
            <span slot="label">
              <i class="el-icon-picture-outline"></i> 组件</span>
            <div style="height: 100%;text-align: center;">
              <img :src=data.svgUrl
                class="images"
                style="height: 95%;width: 100%;" />
            </div>
          </el-tab-pane>
          <el-tab-pane style="height: 100%">
            <span slot="label">
              <i class="el-icon-document"></i> 说明</span>
            <div style="height: 95%;">
              <iframe :src=data.infoUrl
                style="width: 100%;height: 100%;border-width: 0px"></iframe>
            </div>
          </el-tab-pane>
          <el-tab-pane style="height: 100%">
            <span slot="label">
              <i class="el-icon-tickets"></i> 变量</span>

            <div style="height: inherit;">
              <package-diagram-model-child></package-diagram-model-child>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-main>

      <el-aside class="right-aside">

        <el-dialog title="仓库历史"
          :visible.sync="repoHistoryDialog"
          v-if="repoHistoryDialog"
          width="70%"
          :fullscreen=true
          :close-on-click-modal="false">
          <repoHistory :modelId="this.modelId"></repoHistory>
        </el-dialog>
        <el-dialog title="仓库设置"
          :visible.sync="repoSettingDialog"
          v-if="repoSettingDialog"
          width="50%"
          :close-on-click-modal="false">
          <div style="width: auto;height: auto;">
            <repoSetting :modelName="this.form.name"
              @closeDialog="closeDialog"></repoSetting>
          </div>

        </el-dialog>

        <el-dialog title="管理协作者"
          :visible.sync="cDialog"
          v-if="cDialog"
          width="60%"
          :close-on-click-modal="false">
          <collaborator :modelId="this.modelId"
            @dataRefresh="getCollaborators"></collaborator>
        </el-dialog>

        <div style="text-align: center;width: inherit;margin-top: 10px;">

          <el-button type="text"
            @click="showCollaborators"
            size="small">
            协作者
          </el-button>

          <!--   <el-tag type="warning" @click="bbb" >协作者<i class="el-icon-plus el-icon--right"></i></el-tag> -->
          <!--     <span @click="bbb">协作者</span> -->
          <el-table :data="collaborators"
            style="width: 100%;margin-top: 10px;border-top: solid 1px #e6e6e6;"
            :show-header="false">

            <el-table-column prop="name"
              label="协作者">
              <template slot-scope="scope">
                <el-tag type="warning"
                  :title="scope.row.user.name">{{scope.row.user.name}}</el-tag>
              </template>

            </el-table-column>
          </el-table>

        </div>

      </el-aside>
    </el-container>

    <!--<el-footer class="main-footer" style="">-->
    <!--<span>-->
    <!--############################################-->
    <!--</span>-->

    <!--</el-footer>-->
  </el-container>

</template>


<script>
import errGif from '@/assets/401_images/401.gif'
import modeltree from './modelTree.vue'
import modeltreeContAndVar from './modelTreeContAndVar.vue'
import PackageDiagramModelChild from "./packageDiagramModelChild.vue";
import global_ from '../global.vue'
import { mapActions, mapState, mapGetters } from 'vuex'
import '../../assets/jqueryUI/tabs.css'
import collaborator from '../model/collaborator'
import repoHistory from '../model/repoHistory'
import repoSetting from '../model/RepoSetting.vue'
import ElCol from "element-ui/packages/col/src/col";
export default {
  components: {
    ElCol,
    modeltree,
    modeltreeContAndVar,
    PackageDiagramModelChild,
    collaborator,
    repoHistory,
    repoSetting
  },
  data() {
    return {
      //                errGif: errGif + '?' + +new Date(),
      //                props: {
      //                    label: 'name',
      //                    children: 'zones'
      //                },
      //                count: 1,
      //                info:false,
      //                list:true,
      form: [],
      textInfo: '',
      data: {
        svgUrl: '',
        iconUrl: '',
        infoUrl: '',
      },
      name: this.$store.state.userInfo.name,
      collaborators: [],
      cDialog: false,
      repoHistoryDialog: false,
      repoSettingDialog: false,
      cmOptions: {
        lineNumbers: true,
        // lineWrapping: true,
        mode: 'text/x-modelica',
        value: '',
        readOnly: true,
        foldGutter: true,
        gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"]
      },
      websock: null,
      tempModelText: '',
      tempModelTextReadCount: 0,
      cmActive: false
    };
  },
  computed: {
    ...mapState({
      modelId: state => state.modelId,
      a: state => state.a
    }),
    ...mapGetters(['modelId', 'amsg', 'treeModelId']),
    treeModelVar() {
      const _this = this;
      const url = '/api/model/modelVariable?modelId=' + _this.treeModelId;
      _this.$http.post(url)
        .then(function (response) {
          _this.form = response.data.form;
          if (response.data.msg == "error") {
          }
          //  _this.textInfo = response.data.form.textInfo;
          // window.editor.setValue(_this.textInfo);
          if (response.data.form.diagramSvgPath == "") {
            _this.data.svgUrl = ""
          } else {
            _this.data.svgUrl = response.data.form.diagramSvgPath + "?" + new Date().getTime();
          }
          if (response.data.form.iconSvgPath == "") {
            _this.data.iconUrl = ""
          } else {
            _this.data.iconUrl = response.data.form.iconSvgPath;
          }
          if (response.data.form.infoTextPath == "") {
            _this.data.infoUrl = ""
          } else {
            _this.data.infoUrl = response.data.form.infoTextPath;
          }
          _this.getModelText();
        })
        .catch(function (error) {
          console.log(error)
        })
    },

  },
  methods: {
    tabClick(tab) {
      if (!this.cmActive && tab.name == 'cm') {
        this.cmActive = true;
      }
    },
    getModelText() {
      // 参数
      const agentData = this.treeModelId;
      this.textInfo = ''
      if (this.websock != null) {
        // 若是ws开启状态
        if (this.websock.readyState === this.websock.OPEN) {
          this.websocketsend(agentData)
        }
        // 若是 正在开启状态，则等待300毫秒
        else if (this.websock.readyState === this.websock.CONNECTING) {
          const that = this;// 保存当前对象this
          setTimeout(function () {
            that.websocketsend(agentData)
          }, 300);
        }
        // 若未开启 ，则等待500毫秒
        else {
          this.initWebSocket();
          const that = this;// 保存当前对象this
          setTimeout(function () {
            that.websocketsend(agentData)
          }, 500);
        }
      }
    },
    closeDialog() {
      this.repoSettingDialog = false;
    },
    toDetails() {
      this.$router.push('/NewDetails');
    },
    handleCheckChange(data, checked, indeterminate) {
      console.log(data, checked, indeterminate);
    },
    handleNodeClick(data) {
      console.log(data);
    },
    loadNode(node, resolve) {
      if (node.level === 0) {
        return resolve([{ name: 'region1' }, { name: 'region2' }]);
      }
      if (node.level > 3) return resolve([]);

      let hasChild;
      if (node.data.name === 'region1') {
        hasChild = true;
      } else if (node.data.name === 'region2') {
        hasChild = false;
      } else {
        hasChild = Math.random() > 0.5;
      }

      setTimeout(() => {
        let data;
        if (hasChild) {
          data = [{
            name: 'zone' + this.count++
          }, {
            name: 'zone' + this.count++
          }];
        } else {
          data = [];
        }

        resolve(data);
      }, 500);
    },
    isAddStar(item) {
      const _this = this;
      const checkAlreadyStar = '/api/star/checkAlreadyStar?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(checkAlreadyStar)
        .then(function (response) {
          if (response.data.alreadyStar == "1") {
            _this.deleteStar(item);
          }
          if (response.data.alreadyStar == "0") {
            _this.addStar(item);
          }
        })
    },
    addStar(item) {
      const _this = this;
      const url = '/api/star/add?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            item.alreadyStar = true;
            item.numberStar += 1;
            _this.form = item;
          }
        }).catch(function (error) {
          console.log(error);
        });
    },
    deleteStar(item) {
      const _this = this;
      const url = '/api/star/delete?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            item.alreadyStar = false;
            item.numberStar -= 1;
            _this.form = item;
          }
        }).catch(function (error) {
          console.log(error);
        });
    },
    isAddWatch(item) {
      const _this = this;
      const checkAlreadyStar = '/api/watch/checkAlreadyWatch?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(checkAlreadyStar)
        .then(function (response) {
          if (response.data.alreadyWatch == "1") {
            _this.deleteWatch(item);
          }
          if (response.data.alreadyWatch == "0") {
            _this.addWatch(item);
          }
        })
    },
    addWatch(item) {
      const _this = this;
      const url = '/api/watch/add?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            item.alreadyWatch = true;
            item.numberWatch += 1;
            _this.form = item;
          }
        }).catch(function (error) {
          console.log(error);
        });
    },
    deleteWatch(item) {
      const _this = this;
      const url = '/api/watch/delete?userId=' + item.userId + "&repoName=" + item.repositoryName + "&onlineUser=" + _this.name
      _this.$http.post(url)
        .then(function (response) {
          if (response.data.msg == "ok") {
            item.alreadyWatch = false;
            item.numberWatch -= 1;
            _this.form = item;
          }
        }).catch(function (error) {
          console.log(error);
        });
    },
    treeModelRefresh() {
      const _this = this;
      if (_this.modelId == null || _this.modelId == '') {
        this.$router.push({ path: '/model/MyIndex' });
      } else {
        const url = '/api/model/modelVariable?modelId=' + _this.treeModelId;
        _this.$http.post(url)
          .then(function (response) {
            _this.form = response.data.form;
            _this.textInfo = response.data.form.textInfo
            // window.editor.setValue(_this.textInfo);
            if (response.data.form.diagramSvgPath == "") {
              _this.data.svgUrl = ""
            } else {
              _this.data.svgUrl = response.data.form.diagramSvgPath;
            }
            if (response.data.form.iconSvgPath == "") {
              _this.data.iconUrl = ""
            } else {
              _this.data.iconUrl = response.data.form.iconSvgPath;
            }
            if (response.data.form.infoTextPath == "") {
              _this.data.infoUrl = ""
            } else {
              _this.data.infoUrl = response.data.form.infoTextPath;
            }

          })
          .catch(function (error) {
            console.log(error)
          })
      }
    },
    goRepoSetting() {
      this.repoSettingDialog = true;
    },
    goFile() {
      // this.$router.push({path: '/model/fileDetail',query : { modelInfo : this.form }});
      this.$router.push('/model/private/' + this.$route.params.userName + '/' + this.$route.params.repoName + '/fileDetail');
    },
    download() {
      location.href = ('/corporate/' + this.$store.getters.modelInfo.userName + '/' + this.$store.getters.modelInfo.name + '/archive/' + 'master.zip');
    },
    goPublish() {
      // this.$router.push({path:'/model/release'});
      this.$router.push('/model/private/' + this.$route.params.userName + '/' + this.$route.params.repoName + '/release');
    },
    goReposity() {

      const gogsDomain = global_.gogsHostPath;
      const gogsPort = global_.gogsPort;
      const domain = global_.HostPath;
      if (gogsPort.length > 0) {
        window.location.href = "http://" + domain + gogsPort + "/" + this.$data.name + "/" + this.form.name.split(".", 1)[0];
      } else {
        window.location.href = "http://" + domain + '/' + gogsDomain + "/" + this.$data.name + "/" + this.form.name.split(".", 1)[0];
      }
    },
    getCollaborators() {
      this.$http.post('api/collaboration/getCollaboratorsByModelId?modelId=' + this.modelId).then(res => {
        this.collaborators = res.data.collaborators;

      }).catch(error => {
        console.log(error);
      });
    },
    showCollaborators() {
      this.cDialog = true;
    },
    showRepoHistory() {
      this.repoHistoryDialog = true;
    },
    initWebSocket() { // 初始化weosocket
      // ws地址
      const wsuri = "ws://" + process.env.HOST_PATH + ":8080" + "/getModelText";
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onclose = this.websocketclose;
    },
    websocketonmessage(e) { // 数据接收
      if (e.data === 'done') {
        this.textInfo += this.tempModelText
        return;
      }
      if (e.data === 'start') {
        this.textInfo = ''
        this.tempModelText = ''
        this.tempModelTextReadCount = 0
        return
      }
      this.tempModelText += e.data;
      this.tempModelTextReadCount++;
      if (this.tempModelTextReadCount == 1000) {
        this.textInfo += this.tempModelText
        this.tempModelText = ''
        // this.tempModelTextReadCount=0
      }
    },
    websocketsend(agentData) { // 数据发送
      console.log(agentData)
      this.websock.send(agentData);
    },
    websocketclose(e) {  // 关闭
      console.log("connection closed (" + e.code + ")");
    }

  },
  mounted() {

    this.getCollaborators();
    this.initWebSocket();

  }
};

</script>


<style scoped lang="scss">
.main-container {
  height: inherit;
  overflow-y: hidden;
}

.top-header {
  max-height: 53px;
  background-color: #f8f8f8;
}

.middle-header {
  max-height: 50px;
  border-top: solid 1px #e7e7e7;
}

.main-footer {
  max-height: 10px;
  text-align: center;
  border-top: solid 1px #e7e7e7;
}

.left-aside {
  border-right: solid 1px #e7e7e7;
  min-width: 300px;
}

.right-aside {
  border-left: solid 1px #e7e7e7;
  width: 100px !important;
  overflow-x: hidden;
}

/*.el-tabs.el-tabs--bottom{
        height: inherit;
    }
    .el-tab-pane
    {
        height: 100px;
        overflow-y: auto;
    }
    .el-tabs__content{
        overflow: hidden;
        position: relative;
        height: 200px;
    }
    .el-tabs.el-tabs--bottom .el-tabs__header
    {
        height: 400px;
    }
    */

.el-tabs.el-tabs--bottom {
  height: inherit;
  overflow-y: hidden;
}

.el-tab-pane {
  overflow-y: auto;
}

@font-face {
  font-family: "iconfont"; /* project id 445633 */
  src: url("//at.alicdn.com/t/font_445633_4mr7tossw8gjh5mi.eot");
  src: url("//at.alicdn.com/t/font_445633_4mr7tossw8gjh5mi.eot?#iefix")
      format("embedded-opentype"),
    url("//at.alicdn.com/t/font_445633_4mr7tossw8gjh5mi.woff") format("woff"),
    url("//at.alicdn.com/t/font_445633_4mr7tossw8gjh5mi.ttf") format("truetype"),
    url("//at.alicdn.com/t/font_445633_4mr7tossw8gjh5mi.svg#iconfont")
      format("svg");
}
</style>