<template>
  <section>
    <el-container>
      <el-main>
        <el-form ref="form"
          :model="form"
          :rules="rules"
          label-width="80px">
          <el-form-item label="仓库名称"
            prop="name">

            <el-input v-model="form.name"
              :disabled="true"></el-input>

          </el-form-item>
          <el-form-item label="仓库描述">

            <el-input type="textarea"
              :autosize="{ minRows: 5, maxRows: 10}"
              placeholder="请添加相关描述"
              v-model="form.desc"></el-input>

          </el-form-item>
          <el-row>
            <el-col :span="6"
              :offset="18">
              <el-button type="primary"
                :loading="submitLoading"
                @click.native="updateRepo">
                更新仓库
              </el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col>
              <span style="font-size:14px">危险操作：</span>
            </el-col>
          </el-row>
          <el-row style="margin-top: 20px">
            <el-col>
              <span>转移仓库的使用权:</span>
            </el-col>
          </el-row>
          <el-row style="margin-top: 20px">
            <el-col :span="12">
              <el-form-item label="新拥有者:">
                <el-input style='margin-left:15px;width:200px'
                  v-model='newMaster'></el-input>
              </el-form-item>
            </el-col>
            <el-col :offset="6"
              :span="6">
              <el-button type="warning"
                @click.native="transferRepo">转移仓库
              </el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="删除仓库:">
                <el-input style='margin-left:15px;width:200px'></el-input>
              </el-form-item>
            </el-col>
            <el-col :offset="6"
              :span="6">
              <el-button type="danger"
                @click.native="deleteRepo">删除仓库</el-button>
            </el-col>
          </el-row>
        </el-form>
      </el-main>
    </el-container>

  </section>
</template>
<script>
import global_ from '../global.vue'
import { mapActions, mapState, mapGetters } from 'vuex'
export default {
  props: [
    "modelName"
  ],
  components: {
    global_
  },
  data() {
    const validateName = (rule, value, callback) => {
      const re = new RegExp("^[a-zA-Z0-9\u4e00-\u9fa5]+$");
      console.log(value);

      if (!value) {
        callback(new Error('请输入仓库名称'));
      } else {
        if (re.test(value)) {
          if (value.length > 128) {
            callback(new Error('仓库名称字符不得超过128个'));
          } else {
            callback();
          }

        } else {
          callback(new Error('只允许输入中文、字母、数字'));
        }
      }
    };
    return {
      newMaster: '',
      form: {
        name: this.modelName,
        region: '',
        desc: '',
        photoName: '',
        files: [],
        fileLists: [],
        showPicture: true,
      },
      rules: {
        name: [{
          required: true,
          validator: validateName,
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '请输入模型分类',
          trigger: 'blur'
        }],
      },
      submitLoading: false,
      name: this.$store.state.userInfo.name,
    }
  },
  computed: {
    ...mapState({
      modelId: state => state.modelId,
      a: state => state.a
    }),
    ...mapGetters(['modelId', 'bmsg', 'treeModelId']),
  },
  methods: {


    closeDia() {
      this.$emit("closeDialog")
    },
    toModel: function () {
      const _this = this;
      _this.$router.push('/model/MyIndex');
    },

    updateRepo() {
      const checkModelName = '/api/repository/update?name=' + this.form.name + "&desc=" + this.form.desc + "&userName=" + this.name
      const _this = this;
      _this.$http.post(checkModelName)
        .then(function (response) {
          // debugger;

          if (response.data.msg == "success") {
            _this.submitLoading = true;
            _this.$message({
              message: '更新成功！',
              type: 'success',
              duration: 2000
            });
            _this.closeDia();
          } else {
            _this.$message({
              message: '更新失败',
              type: 'error',
              duration: 2000
            });
            // _this.$emit("openInnerVisible",_this.form.name)
          }
        }).catch(function (error) {
          console.log(error)
          _this.submitLoading = false;
          _this.$message({
            message: '更新失败！',
            type: 'error',
            duration: 2000
          });
        })

    },
    goTransferRepo() {

      const checkModelName = '/api/repository/transfer?repositoryName=' + this.form.name + '&newMaster=' + this.newMaster + "&userName=" + this.name
      const _this = this;
      _this.$http.post(checkModelName)
        .then(function (response) {
          if (response.data.msg == "success" && response.data.code == "0") {
            _this.$message({
              message: '转移仓库成功！',
              type: 'success',
              duration: 2000
            });
            _this.toModel();
          } else {
            _this.$message({
              message: '请输入正确的账户',
              type: 'error',
              duration: 2000
            });
            // _this.$emit("openInnerVisible",_this.form.name)
          }
        })
        .catch(function (error) {
          console.log(error)
          _this.submitLoading = false;
          _this.$message({
            message: '删除仓库失败！',
            type: 'error',
            duration: 2000
          });
        })
    },
    doTransfer() {
      const _this = this
      const formData = new FormData();
      const userName = _this.$store.state.userInfo.name;
      formData.append('_csrf', this.$store.getters.csrf);
      formData.append('action', 'transfer');
      formData.append('repo_name', this.form.name);
      formData.append("new_owner_name", this.newMaster)
      const gogsUrl = 'corporate/syslink/' + userName + "/" + this.form.name + "/settingsJson"
      _this.$http({
        method: 'post',
        url: gogsUrl,
        headers: { 'Content-Type': 'multipart/form-data' },
        data: formData,
      }).then(res => {
        const data = res.data
        if (data.code === 0) {
          _this.goTransferRepo()
        } else {
          _this.$message({
            message: data.message,
            type: 'error',
            duration: 2000
          });
        }
      })
    },
    transferRepo() {
      const checkNameUrl = '/api/user/checkNameExist?name=' + this.newMaster
      const _this = this;
      _this.$http.get(checkNameUrl)
        .then(function (response) {
          console.log(response)
          if (response.data.flag) {
            _this.doTransfer()
          } else {
            _this.$message({
              message: '不存在该用户',
              type: 'error',
              duration: 2000
            });
          }
        })
        .catch(function (error) {
          console.log(error)
          _this.$message({
            message: '检测用户名失败！',
            type: 'error',
            duration: 2000
          });
        })
    },
    deleteRepo() {
      this.$confirm('确认删除该仓库吗?', '提示', {
        type: 'warning',
        closeOnClickModal: false,
      }).then(() => {
        const checkModelName = '/api/repository/delete?repositoryName=' + this.form.name + "&userName=" + this.name
        const _this = this;
        _this.$http.post(checkModelName)
          .then(function (response) {
            if (response.data.msg == "success") {
              _this.submitLoading = false;
              _this.$message({
                message: '删除仓库成功！',
                type: 'success',
                duration: 2000
              });
              _this.toModel();
            } else {
              _this.$message({
                message: '删除仓库失败',
                type: 'error',
                duration: 2000
              });
              // _this.$emit("openInnerVisible",_this.form.name)
            }
          })
      }).catch(function (error) {
        console.log(error)
        this.submitLoading = false;
        this.$message({
          message: '删除仓库失败！',
          type: 'error',
          duration: 2000
        });
      })
    }

  }


}

</script>
<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>