<template>
  <section>
    {{getModelVar}}
    <el-input style="width:300px;"
      placeholder="输入关键字进行过滤"
      v-model="filterText">
    </el-input>

    <el-tree class="filter-tree"
      :data="data2"
      :props="defaultProps"
      highlight-current
      accordion
      @node-click="treeNodeClick"
      :filter-node-method="filterNode"
      :render-content="renderContent"
      ref="tree2">
      <!-- <span slot-scope="{ node, data }">
            <el-popover
                    placement="right-start"
                    trigger="hover"
                    :content="data.name+(data.description===undefined?'':'('+data.description+')')+(data.leafKid<=0?'':'('+data.leafKid+')')">
               <span slot="reference">    {{ node.label+(data.description===undefined?'':'('+data.description+')') }} <span v-if="data.leafKid>=1" style="color:red">({{data.leafKid}})</span></span>
            </el-popover>



                     </span>-->
    </el-tree>
  </section>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  },
  computed: {
    ...mapState({
      modelId: state => state.modelId,
      a: state => state.a
    }),
    ...mapGetters(['modelId', 'amsg']),
    getModelVar() {
      const _this = this;
      if (_this.modelId == null || _this.modelId == '') {  
      } else {
        const url = '/api/model/treeModel?modelId=' + _this.modelId;
        //                   this.$store.dispatch('sendTreeModelId',_this.modelId);
        _this.$http.post(url)
          .then(function (response) {
            const data = response.data.data
            /*   data.forEach(item=>{
                          _this.computeLeafCount(item)
                      })*/

            console.log(data)
            _this.data2 = data;
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  },

  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    treeNodeClick(arg) {
      this.$store.dispatch('sendTreeModelId', arg.id);
    },
    computeLeafCount(data) {
      if (data.children === undefined || data.children.length === 0) {
        data.leafKid = 0;
        return;
      }
      data.leafKid = this.getKids(data)
      for (let index = 0; index < data.children.length; index++) {
        this.computeLeafCount(data.children[index])

      }
    },
    getKids(data) {
      if (data.children === undefined || data.children.length === 0) {
        return 1;
      }
      let count = 0;
      for (let kid = 0; kid < data.children.length; kid++) {
        count += this.getKids(data.children[kid])
      }

      return count;
    },
    renderContent(h, { node, data, store }) {
      let level;
      if (node.level > 10) {
        level = 10;
      } else {
        level = node.level;
      }

      return (
        <span style={{ width: 298 - 18 * level + 'px' }} title={node.label} class="spanEllipsis">
          <img v-show="node.data.iconUrl!=='default" src={node.data.iconUrl === 'default' ? this.constant.SVG.DEFAULT : node.data.iconUrl} style="height: 16px;width=16px;display:inline-block;" ></img>
          {node.label}
        </span>

      );


    }
  },

  data() {
    return {
      filterText: '',
      data2: [
      ],
      defaultProps: {
        children: 'children',
        label: 'name',
        iconClass: 'iconClass'
      }
    };
  }
};
</script>
<style scoped>
.iconClassRoot {
  width: 15px;
  height: 15px;
  display: inline-block;
  background: url("../../assets/root.png") no-repeat center/100% auto;
}
.el-tree-node__label {
  width: 298px;
}

/*.el-tree-node>.el-tree-node__children {*/
/*overflow-x: scroll;*/
/*}*/
</style>