<script>

export default {

  name: 'repoHistory',
  props: {
    modelId: {
      type: Number
    }
  },

  data() {
    return {
      actions: [],
      currentRow: {
        message: '',
        diffFiles: []
      },
      history: ''
    }
  },
  methods: {
    currentChange(oldRow, currentRow) {
      this.currentRow.diffFiles = currentRow.DiffFiles;
      this.currentRow.message = currentRow.UserCommit.CommitMessage;

    },
    // getActions() {
    //   this.$http.post('api/action/getRepoAction?modelId=' + this.modelId).then((res) => {
    //     const contents = [];
    //     for (const action of res.data.actions) {
    //       let ac = JSON.stringify(action.content)

    //     }
    //     console.log(contents)
    //     this.actions = res.data.actions;
    //   }).catch(function (error) {
    //     console.log(error);
    //   });

    // },
    statusBar(Addition, Deletion) {
      if (Addition > 0 && Deletion == 0) {
        return '100%'
      }
      if (Addition == 0 && Deletion > 0) {
        return '0%'
      }
      if (Addition > 0 && Deletion > 0) {
        return Addition / (Addition + Deletion) * 100 + '%'
      }

    },
    dateFormatter: function (row, column) {
      if (row.createdUnix != null) {
        return moment(moment.unix(row.createdUnix)).format('YYYY-MM-DD  HH:mm');
      }


    },
    commitTypeCss(type) {

      if (type == 1) {
        return 'add'
      }
      if (type == 2) {
        return 'modified '
      }
      if (type == 3) {
        return 'delete '
      }
      if (type == 4) {
        return 'rename '
      }
    },
    typeFormatter(row, column) {
      if (row.opType != null) {
        if (row.opType == 1) {
          return "创建仓库";
        }
        if (row.opType == 2) {
          return "重命名仓库";
        }
        // if (row.opType == 3) {

        // }
        // if (row.opType == 4) {

        // }
        // if (row.opType == 5) {
        return "提交代码";
      }
      // if (row.opType == 6) {

      // }
      // if (row.opType == 7) {

      // }
      if (row.opType == 8) {
        return "转移仓库";
      }
      // if (row.opType == 9) {

      // }
      // if (row.opType == 10) {

      // }
      // if (row.opType == 11) {

      // }
      // if (row.opType == 12) {

      // }
      // if (row.opType == 13) {

      // }
      // if (row.opType == 14) {

      // }
      // if (row.opType == 15) {

      // }
      if (row.opType == 16) {
        return "创建分支:" + row.ref_name;
      }
      if (row.opType == 17) {
        return "删除分支:" + row.ref_name;
      }
      // if (row.opType == 18) {

      // }
      if (row.opType == 19) {
        return "fork仓库"
      }

    },
    toModel(row) {
      this.$store.dispatch('sendModelId', row.model_id);
      this.$store.dispatch('sendTreeModelId', row.model_id);
      this.$router.push({ path: '/model/packageDiagram' });
    },
    getCommits() {
      this.$http.get('corporate/syslink/' + this.$route.params.userName + '/' + this.$route.params.repoName + '/commits/' + this.constant.GOGS.BRANCH).then((res) => {
        this.actions = res.data.actions;
      })
    },
    searchCommits() {
      this.$http.get('corporate/syslink/' + this.$route.params.userName + '/' + this.$route.params.repoName + '/commits/' + this.constant.GOGS.BRANCH + '/search?q=' + this.history).then((res) => {
        this.actions = res.data.actions;
        console.log(res.data.actions);
      })
    }


  },

  mounted() {
    // let createRepository = { autoInit: true, repoName: 'ggg', decription: '4444', readme: 'package.mo' }
    // this.$http.post('api/repo/create', createRepository)
    // this.getActions();
    // this.$http.get('corporate/syslink/test/zxc/commits/master').then((res) => {
    //   this.actions = res.data.actions;
    // })
    this.getCommits();
  }
}

</script>

<style >
.type-box {
  display: inline-block;
  width: 10px;
  height: 10px;
}

.modified {
  background-color: #f0db88;
}
.add {
  background-color: #b4e2b4;
}
.delete {
  background-color: #e9aeae;
}
.rename {
  background-color: #dad8ff;
}
.status-bar {
