<template>
  <section style="height: inherit;overflow: auto;">

    <div style="height: inherit;">

      <div class="repoMessage">
        {{this.repoMessage}}
      </div>
      <!--  <div class="pushBox"> 

      <span>提交历史</span>
      <span>代码分支</span>
      <span>版本发布</span>
    </div> -->

      <div class="infoBox">

        <div style="float: left;">
          <el-select v-model="select"
            placeholder="请选择"
            @change="selectChange"
            style="width: 150px;">
            <el-option-group v-for="group in options"
              :key="group.label"
              :label="group.label">
              <el-option v-for="item in group.options"
                :key="item.value"
                :label="group.label+':'+item.label"
                :value="item.value">
              </el-option>
            </el-option-group>
          </el-select>
          <!-- <span style="font-size: 1rem;margin-left: 10px;">{{this.repoInfo.Repository.Repository.Name}}</span> -->

        </div>

        <div style="float: right;">
          <span style="align-self: center;font-size: 1rem;margin-right: 10px;">克隆链接:</span>
          <el-input readonly
            v-model="address"
            id="fileUrl"
            style="width: 200px;"> </el-input>

          <el-tooltip class="item"
            effect="dark"
            content="复制链接"
            placement="bottom">
            <el-button icon="el-icon-tickets"
              style="margin-left: 10px;height: 32px;align-self: center;"
              size="small"
              @click="copyUrl"></el-button>
          </el-tooltip>
          <el-tooltip class="item"
            effect="dark"
            content="下载"
            placement="bottom">
            <el-button icon="el-icon-download"
              style="margin-left: 10px;height: 32px;align-self: center;"
              size="small"
              @click="download"></el-button>
          </el-tooltip>

        </div>

        <el-breadcrumb separator="/"
          style="float: left;font-size: 14px;line-height: 40px;margin-left: 10px;">

          <el-breadcrumb-item>
            <span @click="goDir('')"
              style="cursor: pointer;"> {{this.repoName}} </span>
          </el-breadcrumb-item>
          <el-breadcrumb-item v-for="path,index in this.paths">
            <span @click="jumpDIr(path,index)"
              style="cursor: pointer;">{{path}}</span>
          </el-breadcrumb-item>
        </el-breadcrumb>

      </div>

      <div class="tableBox">

        <el-table :data="fileList"
          style="width: 100%;border: solid 1px #e6e6e6;"
          :show-header="true">
          <el-table-column prop="FileName"
            min-width="180"
            label="文件名">
            <template slot-scope="scope">
              <div style="display: flex;"
                v-if="scope.row.IsDir">
                <svg class="icon"
                  aria-hidden="true"
                  style="align-self: center;">
                  <use xlink:href="#icon-wenjianjia1"></use>
                </svg>

                <span style="min-width:60px;margin-left: 5px;cursor: pointer;color: #1e70bf;"
                  v-bind:title="scope.row.FileName"
                  class="tableEllipsis"
                  @click="openDir(scope.row.FileName)">{{ scope.row.FileName }}</span>

              </div>

              <div style="display: flex;"
                v-else>

                <i class="iconfont icon-form"></i>
                <span style="min-width:60px;margin-left: 5px;"
                  v-bind:title="scope.row.FileName"
                  class="tableEllipsis">{{ scope.row.FileName }}</span>
              </div>

            </template>
          </el-table-column>

          <el-table-column prop="Size"
            min-width="60"
            label="文件大小"
            :show-overflow-tooltip=true>

          </el-table-column>
          <!--  <el-table-column prop="CommitID" min-width="60" label="推送ID">
       <template slot-scope="scope">
        <span style="min-width:60px" v-bind:title="scope.row.CommitID"  class="tableEllipsis">{{ scope.row.CommitID }}</span>
       </template>
       </el-table-column>  -->
          <el-table-column prop="Message"
            min-width="180"
            label="推送信息"
            :show-overflow-tooltip=true>

          </el-table-column>
          <el-table-column prop="CommitDate"
            min-width="180"
            :formatter="dateFormatter"
            label="推送时间"
            :show-overflow-tooltip=true>

          </el-table-column>
        </el-table>

      </div>

    </div>

  </section>
</template>

<script>
import util from '../../common/js/util'


export default {


  data() {
    return {

      repoInfo: {},
      fileList: [],
      dialogFormVisible: false,
      dialogVisible: false,
      owner: '',
      repoName: '',
      branchs: [],
      tags: [],
      repoLink: '',
      options: [],
      select: '',
      address: '',
      paths: [],
      repoMessage: ''

    }

  },
  methods: {


    //  getRepoInfo(){
    //  var _this = this;
    //  console.log(this.repoName)
    //    this.$http.get('corporate/syslink/'+_this.owner+'/'+_this.repoName).then(res=>{
    //    this.fileList = res.data.Files;
    //    this.repoInfo = Object.assign({},res.data);
    //    this.branchs = this.repoInfo.Branches;
    //    this.repoLink = this.repoInfo.Repository.RepoLink;
    //    this.tags = this.repoInfo.Tags;
    //    this.select = this.repoInfo.Repository.Repository.DefaultBranch;
    //    this.address = location.origin+this.repoInfo.Repository.RepoLink+'.git';
    //    this.repoMessage = this.repoInfo.Repository.Repository.Description;
    //    this.selectInit();


    //  })
    // },
    init() {
      this.repoInfo = this.$store.getters.repoInfo;
      this.getRepoFiles(this.repoInfo.Repository.Owner.Name, this.repoInfo.Repository.Repository.Name, this.repoInfo.Repository.Repository.DefaultBranch);
      this.owner = this.repoInfo.Repository.Owner.Name;
      this.repoName = this.repoInfo.Repository.Repository.Name;
      this.branchs = this.repoInfo.Branches;
      this.repoLink = this.repoInfo.Repository.RepoLink;
      this.tags = this.repoInfo.Tags;
      this.select = this.repoInfo.Repository.Repository.DefaultBranch;
      this.address = location.origin + this.repoInfo.Repository.RepoLink + '.git';
      this.repoMessage = this.repoInfo.Repository.Repository.Description;
      this.selectInit();
    },
    getRepoFiles(userName, repoName, defaultBranchf) {
      this.$http.get('corporate/syslink/' + userName + '/' + repoName + '/src/' + defaultBranchf).then(res => {

        this.fileList = res.data.Files;
      })
    },
    dateFormatter: function (row, column) {
      if (row.CommitDate != null) {
        const dateStr = row.CommitDate.split(" ");
        const strGMT = dateStr[0] + " " + dateStr[1];
        const date = new Date(Date.parse(strGMT));
        return strGMT;
      }


    },

    goDir(dirPath) {

      const _this = this;
      this.$http.get('corporate/syslink/' + _this.owner + '/' + _this.repoName + '/src/' + this.select + '/' + dirPath).then(res => {
        this.fileList = res.data.Files;
        if (dirPath == "") {
          this.paths = [];
        } else {
          this.paths = dirPath.split('/');
          console.log(dirPath);
        }



      })
    },
    jumpDIr(path, index) {


      const dirArray = this.paths.slice(0, index + 1);
      const dirPath = dirArray.join('/');
      this.goDir(dirPath);



    },

    openDir(dirName) {

      if (this.paths.length == 0) {
        const dirPath = dirName;
        this.goDir(dirPath);
      } else {
        const dirPath = this.paths.join('/') + '/' + dirName;
        this.goDir(dirPath);
        console.log(dirPath);
      }
    },


    selectInit() {
      const bs = [];
      const ts = [];
      for (const b of this.branchs) {
        const branch = { label: '', value: '' };
        branch.label = b;
        branch.value = b;
        bs.push(branch);
      }
      for (const t of this.tags) {
        const tag = { label: '', value: '' };
        tag.label = t;
        tag.value = t;
        ts.push(tag)
      }
      const option1 = { label: '分支', options: [] };
      const option2 = { label: '标签', options: [] };
      option1.options = bs;
      option2.options = ts;
      this.options.push(option1, option2);
      console.log(this.options);
    },
    copyUrl() {
      try {
        const e = document.getElementById('fileUrl');
        e.select();
        document.execCommand("Copy");
        if (document.execCommand('copy', false, null)) {
          this.$message({
            message: '复制成功！',
            type: 'success',
            duration: 2000
          });
        }
      } catch (err) {
        console.log(err);
      }


    },
    download() {
      location.href = this.repoInfo.Repository.RepoLink + '/archive/' + this.repoInfo.Repository.Repository.DefaultBranch + '.zip';
    },
    selectChange(value) {
      this.goDir("");
    }

  },

  created() {

  },

  mounted() {
    //   this.$http.get('api/repository/modelInfo?id='+this.$store.getters.modelId).then(res=>{

    //     console.log(res)
    //     this.owner = res.data.repository.ownerName;
    //     this.repoName = res.data.repository.name;
    //     this.getRepoInfo();



    // }).catch(error=>{

    // })


    this.init();



  }
}

</script>

<style scoped>
.pushBox {
  min-width: 600px;
  height: 50px;
  border: solid 1px #e6e6e6;
  border-radius: 0.5rem;
  margin: 20px 10% 10px 10%;
  padding: 0px;
  display: flex;
  text-align: center;
}

div.pushBox > span {
  flex: auto;
  min-width: 150px;
  font-size: 1rem;
  cursor: pointer;
  padding: 10px;
  align-self: center;
}
.infoBox {
  min-width: 600px;
  height: 50px;

  margin: 20px 10% 10px 10%;
  padding: 0px;
  /* display: flex;*/
  text-align: center;
}
.repoMessage {
  min-width: 600px;

  margin: 20px 10% 10px 10%;
  padding: 0px;
}
div.infoBox > span {
  font-size: 1rem;

  align-self: center;
}

.tableBox {
  margin: 20px 10% 10px 10%;
  padding: 0px;
  min-width: 650px;
}
</style>