<template>

  <div class="kz-tree__wrapper">
    <!-- <div class="kz-tree__top">
      <el-button size="small" icon="plus" type="primary" @click="treeAdd({ id: 0 })">增加分类</el-button>
    </div> -->
    <!--<p>{{getTreeData}}</p>-->
    <el-tree ref="kzTree"
      id="kzTree"
      :data="treeData"
      :props="data.defaultProps"
      :expand-on-click-node="false"
      lazy
      :load="loadTreeNode"
      @node-click="handleNodeClick"
      :render-content="nodeRender"
      class="kz-tree">
    </el-tree>
    <!--dialog-->
    <el-dialog :title="dialog.title"
      :visible.sync="dialog.dialogVisible"
      :close-on-click-modal="false">
      <el-form :model="dialog.form"
        ref="dialogForm"
        :rules="dialog.rules">
        <el-form-item label="分类名称"
          prop="name"
          label-width="120"
          required>
          <el-input v-model="dialog.form.name"
            auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click="cancelSubmit">取 消</el-button>
        <el-button type="primary"
          @click="submitForm"
          :loading="dialog.submiting">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getDirectoryList } from '../../api/api';
import modelList from './model.vue';
import ModelContent from "./index";

//  上传文件
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'kz-tree',
  props: {
    data: {
      type: Object,
      required: true,
      default() {
        return {
          // 默认tree-node的字段名
          defaultProps: {
            children: 'children',
            label: 'name',
            isLeaf: 'isLeaf'
          },
          // 异步ajax地址
          // CURD
          url: {
            C: '',
            U: '',
            R: '',
            D: ''
          }
        }
      }
    }
  },
  data() {
    // 声明保存当前操作分类node对象
    this.__currentNode = null;
    const validateName = (rule, value, callback) => {
      //                if (value.trim() == '') {
      //                    callback(new Error('不能全为空格和空值'));
      //                }else {
      //                    callback();
      //                }
      const re = new RegExp("^[a-zA-Z0-9\u4e00-\u9fa5]+$");
      console.log(value);

      if (!value) {
        callback(new Error('请输入分类目录名称'));
      } else {
        if (re.test(value)) {
          callback();
        } else {
          callback(new Error('只允许输入中文、字母、数字'));
        }
      }
    };
    return {
      treeData: [],
      /* 弹框 */
      dialog: {
        title: '增加分类',
        dialogVisible: false,
        submiting: false,
        form: {
          name: '',
          id: '',
          parent_id: 0
        },
        rules: {
          name: {
            required: true,
            validator: validateName,
            //                  message: '请输入分类名称',
            trigger: 'blur'
          }
          //              name: {
          //                  required: true,
          //                  validator : validateName,
          // //                  message: '请输入分类名称',
          //                  trigger: 'blur'
          //              }
        }
      },
    }
  },
  computed: {
    ...mapState({
      b: state => state.b,
      a: state => state.a
    }),
    ...mapGetters(['amsg', 'treeData']),
    getTreeData() {
      console.log(this.$store.state.treeDatas)
      if (this.$store.state.treeDatas != "") {
        //                  this.treeData = this.$store.state.treeDatas.data;
        //                  this.loadTreeNode(this.$store.state.treeDatas[0]);
        const url = this.data.url.R;
        const para = { parent_id: 0 };
        this.$emit("node-click", para);
        this.fetch(url, para)
          .then(data => {
            console.log(data)
            this.treeData = data;
            resolve(data)
          });

      }
    }
  },
  methods: {
    /* 加载子分类 */
    loadTreeNode(treeItem, resolve) {
      //         const url = this.data.url.R ;
      const url = 'api/directory/list';
      const para = {parent_id: ''};
      if (treeItem.id != null && treeItem.id != "" && treeItem.id != 0 && treeItem.data.id != null) {
        para.parent_id = treeItem.data.id;
        this.$emit("node-click", para);
      }
      //         else if(this.amsg >=0 && this.amsg != null && this.amsg != ''){
      //             var para = { parent_id: this.amsg };
      //             this.$emit("node-click",para);
      //         }
      else if (treeItem.data.id == null) {
        para.parent_id = 0;
        this.$emit("node-click", para);
      } else {
        para.parent_id = treeItem.id;
        this.$emit("node-click", para);
      }
      this.fetch(url, para)
        .then(data => {
          resolve(data)
        });
    },
    /* 点击响应时间 */
    handleNodeClick(args) {
      this.$emit('selectNode', args.id);
    },
    /* 构建分类title及工具 */
    nodeRender(h, { _self, node, data }) {

      let level;
      if (node.level > 8) {
        level = 8;
      } else {
        level = node.level;
      }

      const childrenNodes = data.id === 0 ? [h('span', data.name)] : [
        //        h('svg', {
        //              'class': 'icon',attrs:{'aria-hidden':'true'},domProps:{title:data.name},style:{'vertical-align':'baseline','margin-right':'0.1em'}},[h('use',{attrs:{'xlink:href':'#icon-wenjianjiaweigongxiang'}})]
        //          ),

        h('span', { 'class': 'treeEllipsis', style: { 'width': 400 - level * 18 + 'px' }, domProps: { title: data.name } }, [h('svg', {
          'class': 'icon', attrs: { 'aria-hidden': 'true' }, style: { 'margin-right': '0.1em' }        }, [h('use', { attrs: { 'xlink:href': '#icon-wenjianjiaweigongxiang' } })]
        ), h('span', data.name)]),

      ]


      const childrenNodes1 = data.id === 0 ? [h('span', data.name)] : [
        h('span', { 'class': 'treeEllipsis' }, [h('svg', {
          'class': 'icon', attrs: { 'aria-hidden': 'true' }, domProps: { title: data.name }, style: { 'margin-right': '0.1em' }        }, [h('use', { attrs: { 'xlink:href': '#icon-wenjianjiayigongxiang' } })]
        ), h('span', data.name)]), ]


      if (!data.userName) {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          childrenNodes
        )
      } else {
        return h(
          'div',
          {
            'class': 'el-tree-node__label',
            prop: {
              children: '-'
            }
          },
          childrenNodes1
        )
      }



    },
    /* 添加 */
    treeAdd(treeItem, event, node) {
      this.__currentNode = node
      Object.assign(this.dialog, {
        title: '增加分类',
        dialogVisible: true,
        form: {
          name: '',
          id: '',
          parent_id: treeItem.id
        }
      })
    },
    /* 编辑 */
    treeEdit(treeItem, event, node) {
      this.__currentNode = node
      Object.assign(this.dialog, {
        title: '编辑分类',
        dialogVisible: true,
        form: {
          name: treeItem.name,
          id: treeItem.id,
          parent_id: treeItem.parent_id
        }
      })
    },
    /* 删除 */
    treeDelete(treeItem, event, node) {
      const fetchDelOk = () => {
        // ajax
        const url = this.data.url.D
        this.fetch(url, { id: treeItem.id }, 'post').then(data => {
          // 使用实例对象的removeChild方法
          // hack
          // https://github.com/ElemeFE/element/blob/dev/packages/tree/src/model/node.js#L187
          try {
            node.parent.removeChild(node)
            const url = this.data.url.R
            this.fetch(url, node.data.parentId)
          } catch (err) { console.error(err) }
          // 提示结果
          this.$message({ message: '删除成功', type: 'success', duration: 2000 })
        })
      }
      // 询问提示
      this.$confirm('是否删除此分类?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(fetchDelOk).catch(e => e)
    },
    /* ajax封装 */
    fetch(url, data, type = 'POST') {
      const success = (data, resolve, reject) => {
        if (data.status === 1) {
          resolve(data.data)
        } else {
          //     console.error(data.data.code+":"+ data.data.message)
          reject(data)
        }
      }
      return new Promise((resolve, reject) => {
        if (type.toLowerCase() === 'post') {
          if (data) {
            const dataUrl = Object.keys(data).map(item => item + "=" + data[item])
            url = url + (url.indexOf('?') > -1 ? '' : '?') + dataUrl.join('&')
          }
          this.$http.post(url)
            .then(res => res)
            .then(data => success(data.data, resolve, reject))
        } else {
          if (data) {
            const dataUrl = Object.keys(data).map(item => item + "=" + data[item])
            url = url + (url.indexOf('?') > -1 ? '' : '?') + dataUrl.join('&')
          }

          this.$http.get(url)
            .then(res => res)
            .then(data => success(data.data, resolve, reject),
            )
        }
      })
    },
    /* ######## */
    /* 其他 */
    cancelSubmit() {
      this.dialog.dialogVisible = false
      this.$refs.dialogForm.resetFields()
    },
    submitForm() {
      this.$refs.dialogForm.validate((valid) => {
        if (valid) { // 验证通过
          if (this.$refs.dialogForm.model.parent_id == null) {
            this.$refs.dialogForm.model.parent_id = -1;
          }
          const checkDirNameUrl = '/api/directory/checkDirName?parentId=' + this.__currentNode.data.parentId + '&dirName=' + this.$refs.dialogForm.model.name + '&dirId=' + this.$refs.dialogForm.model.id +
            '&dirParentId=' + this.$refs.dialogForm.model.parent_id + '&userName=' + this.$store.state.userInfo.name
          const _this = this;
          _this.$http.post(checkDirNameUrl)
            .then(function (response) {
              if (response.data.state == 1) {
                _this.fetchAddTreeNode()
              } else {
                _this.$message({
                  message: '请重新输入模型分类名称！',
                  type: 'warning',
                  duration: 2000
                });
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$message({
                message: '请重新输入模型分类名称！',
                type: 'warning',
                duration: 2000
              });
            })
        } else {
          return false
        }
      })
    },
    fetchAddTreeNode() {
      const url = this.dialog.form.id ? this.data.url.U : this.data.url.C
      this.dialog.submiting = true
      this.fetch(url, this.dialog.form, 'post')
        .then(data => {
          /* 隐藏dialog */
          Object.assign(this.dialog, {
            submiting: false,
            dialogVisible: false
          })
          this.$refs.dialogForm.resetFields()
          /* 提示结果 */
          const message = this.dialog.form.id ? '编辑成功' : '添加成功'
          this.$message({ message: message, type: 'success' })

          if (this.dialog.form.id) { // 编辑
            this.__currentNode && this.$set(this.__currentNode, 'data', data)
          } else { // 新增
            /* treeNode api */
            if (this.__currentNode) { // 子分类添加子类
              this.__currentNode.doCreateChildren([data])
            } else if (data.parentId === "0") { // 顶级添加子类
              this.$refs.kzTree.root.doCreateChildren([data])
            }
          }
        })
    },
  },
  components: {
    modelList,
    ModelContent
  },

}
</script>

<style >
#kzTree div.el-tree-node__label {
  height: inherit;
}
</style>
